<template>
  <v-navigation-drawer permanent app clipped width="200">
    <v-list dense fixed clipped class="pt-0">
      <transition
        name="custom-classes-transition"
        enter-active-class="animated fadeIn"
        leave-active-class="animated fadeOut"
        :duration="200"
      >
        <v-card
          flat
          :class="!power_option.isUpload&&!power_option.isCreateFolder?'pt-0 pb-0':'pt-2 pb-2'"
          v-if="power_option.isUpload || power_option.isCreateFolder&&!power_option.isSearching"
        >
          <v-layout wrap>
            <v-flex xs12 class="text-xs-center uploadfile">
              <v-btn
                small
                color="info"
                class="ml-0 mr-0 text-xs-center"
                :disabled="!(power_option.isUpload&&!power_option.isSearching)"
                :style="{width:'174px'}"
                @click="$refs.input.click()"
              >
                <v-icon left small class="mr-1">cloud_upload</v-icon>
                {{$t('cloud.upload')}}
              </v-btn>
              <input
                id="uploadFile"
                type="file"
                ref="input"
                @change="uploadFile($event)"
                :style="{display:'none'}"
              >
            </v-flex>
            <v-flex xs12 class="text-xs-center create-folder">
              <v-btn
                small
                color="success"
                class="ml-0 mr-0 text-xs-center"
                @click="createFolder(flag)"
                :style="{width:'174px'}"
              >
                <v-icon left small class="mr-1">create_new_folder</v-icon>
                {{power_option.createFolderTxt}}
              </v-btn>
            </v-flex>
          </v-layout>
        </v-card>
      </transition>

      <v-divider></v-divider>
      <div class="list">
        <v-list-tile
          v-for="(item, index) in itemList"
          :key="index"
          :to="item.path"
          @click="clickItem(item, index)"
          class="pr-0"
        >
          <v-list-tile-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>{{ item.name }}</v-list-tile-title>
          </v-list-tile-content>
          <v-list-tile-sub-title
            v-if="index === 3"
            class="blue--text text--dark-1 text-xs-right"
            :style="{width:'24px'}"
          >{{downloading===0?'':downloading}}</v-list-tile-sub-title>
          <v-list-tile-sub-title
            v-if="index === 4"
            class="blue--text text--dark-1 text-xs-right"
            :style="{width:'24px'}"
          >{{uploading===0?'':uploading}}</v-list-tile-sub-title>
        </v-list-tile>
      </div>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      itemList: [
        {
          icon: "folder_shared",
          name: this.$t("cloud.myFile"),
          more: "",
          path: "/MyFiles"
        },
        {
          icon: "folder",
          name: this.$t("cloud.publicFile"),
          more: "",
          path: "/PublicFiles"
        },
        {
          icon: "share",
          name: this.$t("cloud.sharedFile"),
          more: "",
          path: "/ShareFiles"
        },
        {
          icon: "cloud_download",
          name: this.$t("cloud.download"),
          more: "",
          path: "/Download"
        },
        {
          icon: "cloud_upload",
          name: this.$t("cloud.upload"),
          more: "",
          path: "/Upload"
        }
      ],
      activeItem: 0
    };
  },
  computed: {
    ...mapGetters([
      "upload_list",
      "download_list",
      "power_option",
      "user_account",
      "current_folderInfo",
      "my_files",
      "public_files",
      "share_files"
    ]),
    uploading() {
      return this.upload_list.filter(e => e.process < 100).length;
    },
    downloading() {
      return this.download_list.filter(e => e.process < 100).length;
    },
    flag() {
      if (this.$route.name === "MyFiles") return "SET_MYFIES";
      if (this.$route.name === "PublicFiles") return "SET_PUBLICFILES";
      if (this.$route.name === "ShareFiles") return "SET_SHAREFIES";
    }
  },
  methods: {
    clickItem(item, index) {
      if (this.activeItem === index) return;
      console.log(item, index, "----");
      this.$store.commit("SET_POWEROPTION", {
        key: "isCreateFolder",
        value: true
      });
      switch (index) {
        case 0: {
          this.$store.commit("SET_POWEROPTION", {
            key: "isUpload",
            value: true
          });
          this.$store.commit("SET_POWEROPTION", {
            key: "createFolderTxt",
            value: this.$t("cloud.newFolde")
          });
          this.$store.commit("SET_POWEROPTION", {
            key: "showSearchButton",
            value: true
          });
          break;
        }
        case 2: {
          this.$store.commit("SET_POWEROPTION", {
            key: "isUpload",
            value: false
          });
          this.$store.commit("SET_POWEROPTION", {
            key: "createFolderTxt",
            value: this.$t("cloud.createAuthFload")
          });
          this.$store.commit("SET_POWEROPTION", {
            key: "showSearchButton",
            value: true
          });
          break;
        }
        case 1: {
          this.$store.commit("SET_POWEROPTION", {
            key: "isUpload",
            value: false
          });
          this.$store.commit("SET_POWEROPTION", {
            key: "createFolderTxt",
            value: this.$t("cloud.createPublicFload")
          });
          this.$store.commit("SET_POWEROPTION", {
            key: "showSearchButton",
            value: true
          });
          break;
        }
        default: {
          this.$store.commit("SET_POWEROPTION", {
            key: "isCreateFolder",
            value: false
          });
          this.$store.commit("SET_POWEROPTION", {
            key: "isUpload",
            value: false
          });
          this.$store.commit("SET_POWEROPTION", {
            key: "showSearchButton",
            value: false
          });
        }
      }
      this.$store.commit("SET_SEARCHLIST", { flag: 1, val: [] });
      this.activeItem = index;
      // this.$store.commit('SET_MYFIES', []);
      // this.$store.commit('SET_SHAREFIES', []);
    },
    createFolder(flag) {
      console.log("flag:::", flag);
      this.$alert.prompt({
        // title: '创建文件夹',this.$t('cloud.createFload')
        title: this.$t("cloud.createFload"),
        inputAttrs: {
          label: this.$t("cloud.folderName"),
          maxlength: 24
        },
        onConfirm: value => {
          let tempFiles;
          let createFloderParam = {
            pid: "", // 云盘父目录id 如果没有父目录，我的文件传'1'，共享文件传'2'。*
            ownerId: this.user_account.id, // 拥有者id* userid
            uploaderName: this.user_account.name, // 上传者名字*
            uploaderId: this.user_account.id, // 上传者id* userid
            encryptFileSize: "", // 加密文件大小
            fileId: "", // 文件ID(默认不填值)
            fileCode: "", // 父节点的fileCode，我的文件跟目录传'10011001'，共享文件根目录传'10011002'*
            fileName: value, // 文件名*
            filePath: "", // 文件路径
            fileRemark: "", // 文件备注
            fileClass: "cloudfile", // 文件分类
            fileSize: "", // 文件大小
            fileType: 2, // 类型 1.文件  2.文件夹  3.团队文件夹  4 特殊文件夹（我的文件2，共享文件传3）*
            md5Hash: "", // md5码
            encryptKey: "" // 加密key
          };
          switch (flag) {
            case "SET_MYFIES": {
              if (this.current_folderInfo.fileId === undefined) {
                createFloderParam.pid = "1";
                createFloderParam.fileCode = "10011001";
              } else {
                createFloderParam.pid = this.current_folderInfo.fileId;
                createFloderParam.fileCode = this.current_folderInfo.fileCode;
              }
              console.log(createFloderParam, "我的文件--创建文件夹--入参");
              if (this.renameFileFilter("1", 2, "", value)) {
                this.$toast.warning({
                  message: this.$t("cloud.createDirFail")
                });
                console.log("有重名的文件夹");
                return;
              }
              this.$service.cloud.addFile(createFloderParam, resp => {
                console.log(resp, "我的文件--创建文件夹--返回", flag);
                if (resp.code === 0) {
                  tempFiles = [resp].concat(this.my_files);
                  console.log("tempFiles", tempFiles);
                  this.$store.commit(flag, tempFiles);
                } else {
                  this.$toast.error({
                    message: this.$t("cloud.createDirFail")
                  });
                  this.$toast.error({ message: this.$t("cloud.createFail" , { value }) });
                }
              });
              break;
            }
            case "SET_PUBLICFILES": {
              if (this.current_folderInfo.fileId === undefined) {
                createFloderParam.pid = "3";
                createFloderParam.fileCode = "10011003";
              } else {
                createFloderParam.pid = this.current_folderInfo.fileId;
                createFloderParam.fileCode = this.current_folderInfo.fileCode;
              }
              createFloderParam.fileType = 4;
              console.log(createFloderParam, "我的文件--创建文件夹--入参");
              if (this.renameFileFilter("2", 4, "", value)) {
                this.$toast.warning({
                  message: this.$t("cloud.createDirFail")
                });
                console.log("有重名的文件夹");
                return;
              }
              this.$service.cloud.createPubDir(createFloderParam, resp => {
                console.log(resp, "公开文件--创建文件夹", flag);
                if (resp.code === 0) {
                  //this.isPowerModal = true;
                  //this.folderData = resp;
                  tempFiles = [resp].concat(this.public_files);
                  this.$store.commit(flag, tempFiles);
                } else {
                  this.$toast.error({ message: this.$t("cloud.createPublicFloadFiled") });
                }
              });
              break;
            }
            case "SET_SHAREFIES": {
              if (this.current_folderInfo.fileId === undefined) {
                createFloderParam.pid = "2";
                createFloderParam.fileCode = "10011002";
              } else {
                createFloderParam.pid = this.current_folderInfo.fileId;
                createFloderParam.fileCode = this.current_folderInfo.fileCode;
              }
              createFloderParam.fileType = 3;
              if (this.renameFileFilter("3", 3, "", value)) {
                this.$toast.warning({
                  message: this.$t("cloud.createDirFail")
                });
                console.log("有重名的文件夹");
                return;
              }
              this.$service.cloud.createTeam(createFloderParam, resp => {
                console.log(resp, "共享文件--创建文件夹", flag);
                if (resp.code === 0) {
                  this.$store.commit("SET_POWERMODAL", true);
                  this.folderData = resp;
                  tempFiles = [resp].concat(this.share_files);
                  this.$store.commit(flag, tempFiles);
                } else {
                  this.$toast.error({ message: this.$t("cloud.createAuthFloadFiled") });
                }
              });
              break;
            }
          }
        }
      });
    },
    uploadFile(e) {
      console.log("上传文件开始：：");
      let file = e.target.files[0];
      console.log("file:", file);
      let fileLocalPath = e.target.files[0].path;
      let lastDian = e.target.files[0].name.lastIndexOf("."); // 查找文件扩展名
      let filesExtend = e.target.files[0].name.substring(lastDian + 1); // 截取出文件扩展名
      let uploadFileSize = e.target.files[0].size;
      let uploadItem = {
        // 构造上传列表数据
        fileName: e.target.files[0].name.substring(0, lastDian),
        fileSuffix: filesExtend,
        uploaderName: this.user_account.name,
        fileSize: uploadFileSize,
        localId: new Date().getTime().toString(),
        process: 0,
        fileClass:
          this.$route.name === "ShareFiles"
            ? "SET_SHAREFIES"
            : this.$route.name === "MyFiles"
            ? "SET_MYFIES"
            : "SET_PUBLICFILES",
        processIndex: this.upload_list.length,
        fileType: 1
      };
      this.$store.commit("SET_UPLOADLIST", { flag: 0, item: uploadItem });
      let uploadFileParam = {
        pid: "", // 云盘父目录id 如果没有父目录，传'1',子目录传当前文件夹的fileId。*
        fileCode: "10011001", // 云盘父目录code（根目录10011001,子目录传当前文件夹fileCode）*
        localId: uploadItem.localId, // 唯一标识ID(本地生成)*
        encryptKey: uploadFileSize > 128 * 1024 * 1024 ? "" : this.guid(), // 加密key*
        localPath: fileLocalPath, // 本地文件地址*
        isResume: true, // 是否支持断点续传*
        fileSize: uploadFileSize // 文件大小
      };
      this.$refs.input.value = "";
      console.log("this.current_folderInfo：：",this.current_folderInfo)
      if (this.current_folderInfo.fileCode === "10011001") {
        uploadFileParam.pid = "1";
        uploadFileParam.fileCode = "10011001";
      } else {
        uploadFileParam.pid = this.current_folderInfo.fileId;
        uploadFileParam.fileCode = this.current_folderInfo.fileCode;
      }
      console.log(uploadFileParam, "上传文件-入参");
      this.$service.cloud.uploadFile(
        uploadFileParam,
        resp => {
          console.log(resp, "上传文件回调");
          if (resp.code === 0) {
            let fileInfo = JSON.parse(resp.info).files[0];
            fileInfo.uploaderName = uploadItem.uploaderName;
            fileInfo.fileName = uploadItem.fileName;
            fileInfo.fileSuffix = uploadItem.fileSuffix;
            fileInfo.createTime = fileInfo.createdAt;
            fileInfo.updateTime = fileInfo.createdAt;
            fileInfo.fileSize = fileInfo.filesize;
            fileInfo.filePath = fileInfo.url;
            fileInfo.fileType = 1;
            fileInfo.fileLocalPath = fileLocalPath;
            fileInfo.ownerId === this.user_account.id;
            console.log(
              uploadFileParam.fileCode,
              this.current_folderInfo.fileCode,
              uploadItem.fileClass
            );
            if (uploadFileParam.fileCode === this.current_folderInfo.fileCode) {
              this.$store.commit(uploadItem.fileClass, {
                flag: 1,
                val: fileInfo
              });
            }
          } else {
            if (resp.code == -16777213) return;
            this.$store.commit("SET_UPLOADLIST", {
              flag: 3,
              localId: uploadItem.localId,
              uploadFail: true
            });
            this.$toast.error({
              message: `${
                uploadItem.fileName ? uploadItem.fileName + "." : ""
              }${uploadItem.fileSuffix}${this.$t("cloud.uploadFailed")}`
            });
          }
        },
        proResp => {
          this.$store.commit("SET_UPLOADLIST", {
            flag: 1,
            localId: uploadItem.localId,
            index: uploadItem.processIndex,
            process: proResp.process
          });
        }
      );
    },
    renameFileFilter(tableType, fileType, fileSuffix, name) {
      let fileArray = this.my_files;
      switch (tableType) {
        case "1": {
          fileArray = this.my_files;
          break;
        }
        case "3": {
          fileArray = this.share_files;
          break;
        }
        case "2": {
          fileArray = this.public_files;
          break;
        }
      }
      console.log(
        "fileArray:",
        fileArray,
        "fileType:",
        fileType,
        "name:",
        name
      );
      return fileArray.some((item, index) => {
        if (
          item.fileType === fileType &&
          item.fileName === name &&
          fileSuffix === item.fileSuffix
        ) {
          return true;
        }
      });
    },
    guid() {
      return "xxxxxxxxxxxxxxxx".replace(/[xy]/g, function(c) {
        let r = (Math.random() * 16) | 0,
          v = c == "x" ? r : (r & 0x3) | 0x8;
        return v.toString(16);
      });
    }
  }
};
</script>

<style lang="scss" scoped>
</style>



