<template>
  <v-app>
    <left-list></left-list>
    <nav-bar></nav-bar>
    <v-content>
      <v-container class="ma-0 pa-0" fluid fill-height>
        <v-layout>
            <router-view/>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import { mapGetters } from "vuex";
import NavBar from "./../navbar/index.vue";
import LeftList from "./../list-page/index.vue";
import cloudMixin from "./../common/cloudMixin.js";
export default {
  mixins: [cloudMixin],
  components: {
    NavBar,
    LeftList
  },
  created() {
    this.$service.user.getAccountInfo(null, resp => {
      console.log("get account info: ", resp);
      this.$store.commit("SET_ACCOUNT", resp);
    })
  }
};
</script>

<style lang="scss" scoped>
</style>



