<template>
  <div class="cloud-table">
    <w-menu
      :contextmenuList="menuList"
      :isContextmenu="isContextmenu"
      :contextmenuPos="contextmenuPos"
      :parenteItem="parenteItem"
      @select-item="contextmenuItem"
      @close="isContextmenu=false"
    ></w-menu>
    <v-data-table
      :headers="tableTitle"
      :items="orderByData"
      class="elevation-1"
      disable-initial-sort
      :loading="this.power_option.isLoading"
      :no-data-text="this.power_option.isLoading?$t('cloud.loading'):$t('cloud.noData')"
      hide-actions
      item-key="fileName"
      :style="{height:'100%'}"
    >
      <template v-slot:items="props">
        <tr
          @click="selectItem(props.item, props.index)"
          @dblclick="openFolder(props.item, props.index)"
          @mouseover="mouseoverItem(props.item, props.index)"
          :title="getFileName(props.item.fileName, props.item.fileSuffix)"
          :style="{background: props.index === clickItem?rowClickColor:''}"
          @contextmenu.prevent="openContextmenuList(props.item, props.index, $event)"
        >
          <td :style="getTdStyle" class="td-file-name pr-2">
            <v-avatar size="36px" class="mr-2">
              <img :src="iconFontFn(props.item.fileSuffix, props.item.fileType)">
            </v-avatar>
            <div class="center">
              <div class="name">{{getFileName(props.item.fileName, props.item.fileSuffix)}}</div>
              <div class="uploader-name">
                {{$t('cloud.from')}}:
                <span>{{props.item.uploaderName}}</span>
              </div>
            </div>
            <div class="right">
              <!-- 设置权限 -->
              <v-icon
                v-if="props.item.fileType === 3 && props.item.ownerId === user_account.id&&tableType===3"
                :title="$t('cloud.permission')"
                @click="powerAdmin(props.item, props.index)"
                size="18"
                color="blue-grey darken-2"
                class="mr-1"
              >mdi-shield-account-outline</v-icon>
              <!-- 打开文件夹 或 下载 -->
              <v-icon
                size="18"
                color="blue-grey darken-2"
                :title="props.item.fileLocalPath === ''?$t('cloud.download'):$t('cloud.openFolder')"
                @click="download(props.item, props.index, props.item.fileLocalPath !== '')"
                v-if="props.item.fileType === 1 && isDownload"
                class="mr-1"
              >{{props.item.fileLocalPath === ''?'mdi-arrow-collapse-down':'mdi-folder-open-outline'}}</v-icon>
              <!-- 删除文件 或 文件夹 -->
              <v-icon
                :title="props.item.fileType === 1? $t('cloud.deleteFile'):$t('cloud.deleteDir') "
                @click="delFile(props.item, props.index)"
                v-if="isDelete"
                size="18"
                color="blue-grey darken-2"
                class="mr-1"
              >mdi-trash-can-outline</v-icon>
            </div>
          </td>
          <td
            class="text-xs-center small-th"
          >{{getFileSize(props.item.encryptFileSize || props.item.fileSize)}}</td>
          <td class="text-xs-center" v-if="!isProcessBar">{{getLocalTime(props.item.createTime)}}</td>
          <td class="text-xs-center" v-if="!isProcessBar">{{getLocalTime(props.item.updateTime)}}</td>
          <td class="text-xs-center" v-if="isProcessBar">
            <div class="process-bar">
              <div class="process" :style="{width: props.item.process + '%'}"></div>
            </div>
          </td>
          <td
            class="text-xs-center"
            v-if="isProcessBar&&!props.item.uploadFail"
          >{{props.item.process}}%</td>
          <td class="text-xs-left" v-if="props.item.uploadFail">{{$t('cloud.failed')}}</td>
        </tr>
      </template>
    </v-data-table>
    <move-modal @refreshData="refreshData(flag)"></move-modal>
  </div>
</template>

<script>
import _ from "lodash";
import { mapGetters } from "vuex";
import config from "./../config.js";
import cloudMixin from "../cloudMixin.js";
import MoveModal from "../other/moveModal.vue";
export default {
  mixins: [cloudMixin],
  components: {
    MoveModal
  },
  data() {
    return {
      clickItem: -1,
      isContextmenu: false,
      parenteItem: null,
      contextmenuPos: {
        top: "",
        left: ""
      },
      menuList: [this.$t("cloud.reName")],
      loading: true,
      folderData: {}
    };
  },
  props: {
    isRename: Boolean,
    isDownload: Boolean,
    isDelete: Boolean,
    tableData: Array | Object,
    rowClickColor: {
      type: String,
      default: "#eee"
    },
    isProcessBar: {
      type: Boolean,
      default: false
    },
    yunFileType: String,
    tableType: 0,
    tableTitle: Array | Object
  },
  computed: {
    ...mapGetters([
      "power_option",
      "user_account",
      "search_list",
      "my_files",
      "share_files",
      "public_files",
      "current_folderInfo",
      "download_list",
      "powerModal_state"
    ]),
    orderByData() {
      // this.loading = false;
      return _.orderBy(
        this.tableData,
        ["fileType", "createTime"],
        ["desc", "desc"]
      );
    },
    getTdStyle() {
      return {
        display: "flex",
        "justify-content": "flex-start",
        "align-items": "center",
        position: "relative"
      };
    },
    flag() {
      if (this.$route.name === 'MyFiles') return 'SET_MYFIES';
      if (this.$route.name === 'PublicFiles') return 'SET_PUBLICFILES';
      if (this.$route.name === 'ShareFiles') return 'SET_SHAREFIES';
    }
  },
  methods: {
    mouseoverItem(item, index) {
      // console.log(
      //   "item:",
      //   item,
      //   "this.user_account.id:",
      //   this.user_account.id,
      //   "this.$route.name:",
      //   this.$route.name
      // );
      if (this.$route.name === "ShareFiles") {
        if (
          this.current_folderInfo.fileCode === "10011002" &&
          item.ownerId !== this.user_account.id
        ) {
          this.$store.commit("SET_POWEROPTION", {
            key: "isRename",
            value: false
          });
          this.$store.commit("SET_POWEROPTION", {
            key: "isDelete",
            value: false
          });
          this.$store.commit("SET_POWEROPTION", {
            key: "isDownload",
            value: false
          });
        }
      } else if (this.$route.name === "PublicFiles") {
        if (item.ownerId !== this.user_account.id) {
          this.$store.commit("SET_POWEROPTION", {
            key: "isRename",
            value: false
          });
          this.$store.commit("SET_POWEROPTION", {
            key: "isDelete",
            value: false
          });
          this.$store.commit("SET_POWEROPTION", {
            key: "isDownload",
            value: true
          });
        }
      }
      if (this.power_option.isSearching) {
        this.$store.commit("SET_POWEROPTION", {
          key: "isRename",
          value: false
        });
        this.$store.commit("SET_POWEROPTION", {
          key: "isDelete",
          value: false
        });
        this.$store.commit("SET_POWEROPTION", {
          key: "isDownload",
          value: true
        });
      }
      if (item.ownerId === this.user_account.id) {
        this.$store.commit("SET_POWEROPTION", { key: "isRename", value: true });
        this.$store.commit("SET_POWEROPTION", { key: "isDelete", value: true });
        this.$store.commit("SET_POWEROPTION", {
          key: "isDownload",
          value: true
        });
      }
    },
    // 右键菜单
    openContextmenuList(item, index, evt) {
      this.menuList = [];
      if (this.isRename) {
        this.menuList.push(this.$t("cloud.reName"));
        this.menuList.push(this.$t("cloud.moveTo"));
      }
      if (item.fileLocalPath !== "" && item.fileType === 1) {
        this.menuList.push(this.$t("cloud.deleteLocalFile"));
      }
      if (!this.menuList.length) return;
      this.clickItem = index;
      this.contextmenuPos.left = evt.clientX + "px";
      this.contextmenuPos.top = evt.clientY + "px";
      this.parenteItem = { parenteIndex: index, item: item };
      this.isContextmenu = true;
    },
    // 右键菜单选择
    contextmenuItem(pt, item, index) {
      console.log("this.tableType:", this.tableType);
      console.log(pt, item, index);
      if (item === this.$t("cloud.deleteLocalFile")) {
        //删除本地文件
        this.$alert.confirm({
          confirmText: `${this.$t("cloud.deleteLocalFile")}?`,
          title: this.$t("cloud.Warning"),
          onConfirm: () => {
            this.deleteLocalFile(pt.item);
            if (this.power_option.isSearching) {
              this.$store.commit("SET_SEARCHLIST", {
                flag: 3,
                fileId: pt.item.fileId,
                val: ""
              });
            }
            if (this.tableType === 1)
              this.$store.commit("SET_MYFIES", {
                flag: 3,
                val: "",
                fileId: pt.item.fileId
              });
            else if (this.tableType === 2)
              this.$store.commit("SET_PUBLICFILES", {
                flag: 3,
                val: "",
                fileId: pt.item.fileId
              });
            else if (this.tableType === 3)
              this.$store.commit("SET_SHAREFIES", {
                flag: 3,
                val: "",
                fileId: pt.item.fileId
              });
          }
        });
      } else if (item === this.$t("cloud.reName")) {
        //重命名
        this.renameFile(pt, item, index);
      } else if (item === this.$t("cloud.moveTo")) {
        //移动文件/文件夹
        console.log("移动了文件夹");
        this.$store.commit("SET_POWEROPTION", {
          key: "moveFileInfo",
          value: pt.item
        });
        this.$store.commit("SET_POWEROPTION", {
          key: "showMoveModal",
          value: true
        });
      }
    },
    renameFile(pt, item, index) {
      this.$alert.prompt({
        title: this.$t("cloud.reName"),
        inputAttrs: {
          placeholder: this.$t("cloud.inputhfileName"),
          maxlength: 24
        },
        onConfirm: value => {
          console.log("value::", value);
          console.log("pt.item", pt.item);
          if (pt.item.fileName === value || !value) return;
          if (
            this.renameFileFilter(
              this.tableType,
              pt.item.fileType,
              pt.item.fileSuffix,
              value
            )
          ) {
            this.$toast.warning({
              message:
                pt.item.fileType === 1
                  ? this.$t("cloud.renameFileFail")
                  : this.$t("cloud.renameDirFail")
            });
            console.log("有重名的文件");
            return;
          }
          this.$service.cloud.renameFile(
            {
              fileId: pt.item.fileId,
              fileName: pt.item.fileSuffix
                ? value + "." + pt.item.fileSuffix
                : value
            },
            resp => {
              // console.log('newName:',pt.item.fileSuffix?value+'.'+pt.item.fileSuffix:value)
              // console.log('重名的文件失败:',resp);
              if (resp.code === 0) {
                let updateTime = new Date().getTime() + ''
                if (this.power_option.isSearching) {
                  this.$store.commit("SET_SEARCHLIST", {
                    flag: 2,
                    fileId: pt.item.fileId,
                    val: value,
                    updateTime
                  });
                }
                if (this.tableType === 1)
                  this.$store.commit("SET_MYFIES", {
                    flag: 2,
                    val: value,
                    fileId: pt.item.fileId,
                    updateTime
                  });
                else if (this.tableType === 2)
                  this.$store.commit("SET_PUBLICFILES", {
                    flag: 2,
                    val: value,
                    fileId: pt.item.fileId,
                    updateTime
                  });
                else if (this.tableType === 3)
                  this.$store.commit("SET_SHAREFIES", {
                    flag: 2,
                    val: value,
                    fileId: pt.item.fileId,
                    updateTime
                  });
                if (pt.item.fileType === 1 && pt.item.fileLocalPath !== "") {
                  let newfileName =
                    this.$dbPath +
                    (pt.item.fileSuffix
                      ? value + "." + pt.item.fileSuffix
                      : value);
                  this.$service.file.fileOperate({
                    filepath: pt.item.fileLocalPath,
                    type: 3,
                    newName: newfileName
                  });
                  console.log(
                    "pt.item.fileLocalPath:",
                    pt.item.fileLocalPath,
                    "pt.item.fileName",
                    pt.item.fileName,
                    "value",
                    value,
                    "pt.item.fileSuffix:",
                    pt.item.fileSuffix
                  );
                }
              } else {
                this.$toast.error({
                  message: `${this.$t("cloud.renameFail")}:${resp.code}`
                });
              }
            }
          );
        }
      });
    },
    renameFileFilter(tableType, fileType, fileSuffix, name) {
      let fileArray = this.my_files;
      switch (tableType) {
        case 1: {
          fileArray = this.my_files;
          break;
        }
        case 2: {
          fileArray = this.public_files;
          break;
        }
        case 3: {
          fileArray = this.share_files;
          break;
        }
      }
      console.log(
        "fileArray:",
        fileArray,
        "tableType:",
        tableType,
        "fileType:",
        fileType,
        "name:",
        name
      );
      return fileArray.some((item, index) => {
        if (
          item.fileType === fileType &&
          item.fileName === name &&
          fileSuffix === item.fileSuffix
        ) {
          return true;
        }
      });
    },
    // 选择当前条目
    selectItem(items, index) {
      console.log("items", items);
      console.log("index:", index);
      this.clickItem = index;
    },
    // 双击打开文件夹
    openFolder(items, index) {
      this.clickItem = index;
      if (items.fileType === 1) return;
      this.$emit("row-dbclick", items, index);
      this.$store.commit("SET_CUURENTFOLDERINFO", items);
    },
    // 删除文件
    delFile(items, index) {
      console.log(items, index);
      this.$emit("row-delete", items, index);
    },
    // 下载文件
    download(items, index, isPath) {
      let hasFile;
      if (isPath) {
        if (items.fileLocalPath !== this.getLocalEncryptFilePath(items)) {
          //此文件是用户本次上传的或者未加密下载到本地，因此不在下载目录下
          if (
            this.$service.file.fileOperate({
              filepath: items.fileLocalPath,
              type: 0
            })
          ) {
            this.$shell.showItemInFolder(items.fileLocalPath);
          } else {
            this.reDownloadFile(items, index);
          }
          return;
        }
        let hasFile = this.$service.file.fileOperate({
          filepath: this.getLocalFileDecryptPath(items),
          type: 0
        });
        // 判断文件是否已下载，如果下载打开文件夹
        console.log("打开文件夹时检查本地是否有当前文件:::", hasFile);
        if (hasFile) {
          this.$shell.showItemInFolder(this.getLocalFileDecryptPath(items));
        } else {
          let hasDownload = false;
          hasDownload = this.$service.file.fileOperate({
            filepath: this.getLocalEncryptFilePath(items),
            type: 0
          });
          if (hasDownload) {
            this.decryptFile(this.getLocalEncryptFilePath(items), items, index);
          } else {
            this.reDownloadFile(items, index);
          }
        }
        return;
      }
      // 判断文件是否正在下载
      let isDownLoading = false;
      this.download_list.forEach(element => {
        if (items.fileId === element.fileId && element.process < 100) {
          this.$toast.info({ message: this.$t("cloud.downloading") });
          isDownLoading = true;
        }
      });
      if (isDownLoading) return;
      let hasDbPath = this.$service.file.fileOperate({
        filepath: this.$dbPath + "cache/",
        type: 0
      });
      if (!hasDbPath)
        this.$service.file.fileOperate({
          filepath: this.$dbPath + "cache/",
          type: 1
        });
      let localSavePath = "";
      if (items.hasOwnProperty("encryptKey") && items.encryptKey) {
        localSavePath = this.getLocalEncryptFilePath(items);
      } else {
        localSavePath = this.getLocalFileDecryptPath(items);
      }
      console.log(localSavePath, "选择的文件保存的路径");
      let downloadFileParam = {
        localPath: localSavePath, // 文件本地保存路径
        url: items.filePath // 远程路径,从消息JSON中解析出来的路径 remotePath
      };
      // items.process = 0;
      this.$set(items, "process", 0);
      this.$set(items, "processIndex", this.download_list.length);
      let downFileLocalId = this.$service.file.downloadFile(
        downloadFileParam,
        resp => {
          console.log(resp, "下载文件返回");
          if (resp.code !== 0) return;
          if (items.hasOwnProperty("encryptKey") && items.encryptKey) {
            this.decryptFile(localSavePath, items, index);
            return;
          }
          this.$shell.showItemInFolder(this.getLocalFileDecryptPath(items));
          if (this.power_option.isSearching) {
            this.$store.commit("SET_SEARCHLIST", {
              flag: 3,
              fileId: items.fileId,
              index: index,
              val: this.getLocalFileDecryptPath(items)
            });
          }
          this.$store.commit(this.yunFileType, {
            flag: 3,
            fileId: items.fileId,
            index: index,
            val: this.getLocalFileDecryptPath(items)
          });
        },
        pro => {
          console.log(pro, "文件下载进度返回");
          this.$store.commit("SET_DOWNLOADLIST", {
            flag: 1,
            index: items.processIndex,
            fileId: items.fileId,
            process: pro.progess
          });
        }
      );
      items.localId = downFileLocalId;
      this.$store.commit("SET_DOWNLOADLIST", { flag: 0, item: items });
    },
    // 重新加载文件
    reDownloadFile(items, index) {
      this.$alert.confirm({
        confirmText: this.$t("cloud.fileNot"),
        title: this.$t("cloud.prompt"),
        onConfirm: () => {
          this.download(items, index, false);
        },
        onCancel: () => {
          console.log("取消下载,删除保存的路径:::", this.yunFileType, index);
          this.$store.commit(this.yunFileType, {
            flag: 3,
            index: index,
            val: ""
          });
        }
      });
    },
    // 解密文件
    decryptFile(localSavePath, items, index) {
      let decryptFileParam = {
        encryptkey: items.encryptKey,
        srcpath: localSavePath,
        destpath: this.getLocalFileDecryptPath(items),
        flag: 1
      };
      console.log("解密文件入参:::", decryptFileParam);
      this.$service.file.decryptFile(decryptFileParam, resp => {
        console.log("解密文件返回:::", resp);
        this.$shell.showItemInFolder(decryptFileParam.destpath);
        console.log("this.yunFileType:", this.yunFileType);
        if (this.power_option.isSearching) {
          this.$store.commit("SET_SEARCHLIST", {
            flag: 3,
            fileId: items.fileId,
            val: decryptFileParam.srcpath
          });
        }
        this.$store.commit(this.yunFileType, {
          flag: 3,
          index: index,
          fileId: items.fileId,
          val: decryptFileParam.srcpath
        });
      });
    },
    // 设置授权人员
    powerAdmin(items, index) {
      this.$emit("power-admin", items, index);
    },
    // 获取文件名
    getFileName(n, f) {
      if (n === "") {
        return f;
      } else if (f === "") {
        return n;
      }
      return n + "." + f;
    },
    // 获取文件icon
    iconFontFn(suffix, fileType) {
      if (fileType === 1) {
        let icon =
          config.icon[suffix] === undefined
            ? config.icon.default
            : config.icon[suffix];
        return icon;
      }
      return config.icon.folder;
    },
    // 获取文件大小
    getFileSize(size) {
      if (size === "0") return "-";
      return size > 1024 * 1024
        ? (Math.round((size * 100) / (1024 * 1024)) / 100).toString() + "MB"
        : (Math.round((size * 100) / 1024) / 100).toString() + "KB";
    },
    // 获取时间
    getLocalTime(timestamp) {
      let ttp = timestamp ? timestamp.toString().substr(0, 10) : "0";
      let date = new Date(parseInt(ttp) * 1000)
        .toLocaleString()
        .replace(/:\d{1,2}$/, " ");
      return date.split(" ")[0];
    },
    closeModal() {
      this.isPowerModal = false;
    },
    refreshData(flag) {
      let items = {
        fileId: "1",
        ownerId: this.user_account.id
      };
      let tempObj = {};
      console.log(flag, "======");
      if (flag === "SET_MYFIES") {
        if (this.my_files.length === 0) return;
        if (this.my_files[0].pid === "1") {
          this.queryFloder(items, flag);
        } else {
          Object.assign(tempObj, this.my_files[0]);
          tempObj.fileId = this.my_files[0].pid;
          this.queryFloder(tempObj, flag);
        }
      } else if (flag === "SET_SHAREFIES") {
        if (this.share_files.length === 0) return;
        if (this.share_files[0].pid === "2") {
          this.getTeams();
        } else {
          Object.assign(tempObj, this.share_files[0]);
          tempObj.fileId = this.share_files[0].pid;
          this.queryFloder(tempObj, flag);
        }
      } else if (flag === "SET_PUBLICFILES") {
        if (this.public_files.length === 0) return;
        if (this.public_files[0].pid === "3") {
          this.queryFloder({ fileId: "3" }, flag);
        } else {
          Object.assign(tempObj, this.share_files[0]);
          tempObj.fileId = this.public_files[0].pid;
          this.queryFloder(tempObj, flag);
        }
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.cloud-table {
  height: calc(100% - 41px);
  width: 100%;
  overflow-y: auto;
  .td-file-name {
    .center {
      text-overflow: ellipsis;
      width: 150px;
      .name {
        color: #000;
        width: 100%;
        text-overflow: -o-ellipsis-lastline;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      .uploader-name {
        font-size: 12px;
        color: #9e9e9e;
        span {
          color: #1296db;
        }
      }
    }
    .right {
      width: 60px;
      font-size: 20px;
      text-align: start;
      position: absolute;
      right: 0;
      display: flex;
      justify-content: flex-end;
      i {
        margin-right: 10px;
        display: none;
        cursor: pointer;
        &:hover {
          color: #1296db !important;
        }
      }
    }
  }
  .process-bar {
    background: rgba(18, 150, 219, 0.25);
    width: 90%;
    height: 5px;
    .process {
      transition: width 0.3s;
      height: 100%;
      background: #1296db;
    }
  }
}
</style>



