<template>
  <div class="my-files">
    <bread-crumbs :crumbsList="crumbsList" @switch-to="switchTo" @go-back="goBack"></bread-crumbs>
    <cloud-table
      :tableData="tableData"
      :tableTitle="tableTitle"
      :rowClickColor="'#eaf3ff'"
      :isRename="power_option.isSearching? power_option.isRename:true "
      :isDownload="power_option.isSearching? power_option.isDownload:true"
      :isDelete="power_option.isSearching? power_option.isDelete:true"
      :yunFileType="'SET_MYFIES'"
      @row-dbclick="openFolder"
      @row-delete="delMyFile"
      @download-file="downloadMyFile"
      :tableType="1"
      :style="{position: 'absolute',top: '41px',left: '0px'}"
    ></cloud-table>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import CloudTable from "./../common/table/table.vue";
import cloudMixin from "./../common/cloudMixin.js";
import BreadCrumbs from "./../common/other/bread-crumbs.vue";
export default {
  mixins: [cloudMixin],
  components: {
    CloudTable,
    BreadCrumbs
  },
  data() {
    return {
      crumbsList: [
        { text: this.$t("cloud.allFiles"), fileId: "1", fileCode: "10011001" }
      ]
    };
  },
  computed: {
    ...mapGetters(["user_account", "my_files", "search_list"]),
    tableData() {
      if (!this.power_option.isSearching) {
        return this.my_files;
      } else {
        return this.search_list;
      }
    }
  },
  created() {
    this.queryFloder({ fileId: "1" }, "SET_MYFIES");
    this.$store.commit("SET_CUURENTFOLDERINFO", {});
    this.$store.commit("SET_CUURENTFOLDERINFO", this.crumbsList[0]);
  },
  methods: {
    // 打开文件夹
    openFolder(items, index) {
      this.crumbsList = this.noRepeatCrumbs(items, this.crumbsList);
      this.$store.commit("SET_MYFIES", []);
      this.queryFloder(items, "SET_MYFIES");
    },
    // 删除选中的文件
    delMyFile(items, index) {
      this.delFile(items, index, "SET_MYFIES");
    },
    // 下载文件
    downloadMyFile(items, index) {
      console.log(items, index, this.$t("cloud.download"));
      this.downLoadFile(items);
    },
    // 切换到相应的文件夹
    switchTo(item) {
      let index = this.crumbsList.indexOf(item)
      this.$store.commit("SET_MYFIES", []);
      this.crumbsList = this.crumbsList.slice(0, index + 1);
      this.queryFloder(item, "SET_MYFIES");
    },
    // 返回上一个文件夹
    goBack() {
      this.crumbsList.pop();
      this.$store.commit("SET_MYFIES", []);
      this.queryFloder(
        this.crumbsList[this.crumbsList.length - 1],
        "SET_MYFIES"
      );
    }
  }
};
</script>

<style lang="scss" scoped>
.my-files {
  width: 100%;
  height: 100%;
}
</style>



