<template>
  <div class="public-files">
    <bread-crumbs :crumbsList="crumbsList" @switch-to="switchTo" @go-back="goBack"></bread-crumbs>
    <cloud-table
      :tableData="tableData"
      :tableTitle="tableTitle"
      :rowClickColor="'#eaf3ff'"
      :isRename="power_option.isRename"
      :isDownload="power_option.isDownload"
      :isDelete="power_option.isDelete"
      :yunFileType="'SET_PUBLICFILES'"
      @row-dbclick="openFolder"
      @row-delete="delMyFile"
      @download-file="downloadMyFile"
      :tableType="2"
      :style="{position: 'absolute',top: '41px',left: '0px'}"
    ></cloud-table>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import CloudTable from "./../common/table/table.vue";
import cloudMixin from "./../common/cloudMixin.js";
import BreadCrumbs from "./../common/other/bread-crumbs.vue";
export default {
  mixins: [cloudMixin],
  components: {
    CloudTable,
    BreadCrumbs
  },
  data() {
    return {
      crumbsList: [
        { text: this.$t("cloud.allFiles"), fileId: "3", fileCode: "10011003" }
      ]
    };
  },
  computed: {
    ...mapGetters([
      "user_account",
      "public_files",
      "power_option",
      "search_list"
    ]),
    tableData() {
      if (!this.power_option.isSearching) {
        return this.public_files;
      } else {
        return this.search_list;
      }
    }
  },
  created() {
    this.queryFloder({ fileId: "3" }, "SET_PUBLICFILES");
    this.$store.commit("SET_CUURENTFOLDERINFO", {});
    this.$store.commit("SET_CUURENTFOLDERINFO", this.crumbsList[0]);
  },
  methods: {
    // 打开文件夹
    openFolder(items, index) {
      this.power_option.isUpload = true;
      this.crumbsList = this.noRepeatCrumbs(items, this.crumbsList);
      this.$store.commit("SET_PUBLICFILES", []);
      this.queryFloder(items, "SET_PUBLICFILES");
    },
    // 删除选中的文件
    delMyFile(items, index) {
      this.delFile(items, index, "SET_PUBLICFILES");
    },
    // 下载文件
    downloadMyFile(items, index) {
      console.log(items, index, this.$t("cloud.download"));
      this.downLoadFile(items);
    },
    setPower() {
      let item = this.crumbsList[this.crumbsList.length - 1];
      if (
        this.crumbsList.length === 1 ||
        item.ownerId === this.user_account.id
      ) {
        if (this.crumbsList.length === 1) {
          this.$store.commit("SET_POWEROPTION", {
            key: "isUpload",
            value: false
          });
          this.$store.commit("SET_POWEROPTION", {
            key: "isCreateFolder",
            value: true
          });
        }
        if (item.ownerId === this.user_account.id) {
          this.$store.commit("SET_POWEROPTION", {
            key: "isUpload",
            value: true
          });
          this.$store.commit("SET_POWEROPTION", {
            key: "isCreateFolder",
            value: true
          });
        }
      } else {
        this.$store.commit("SET_POWEROPTION", {
          key: "isUpload",
          value: false
        });
        this.$store.commit("SET_POWEROPTION", {
          key: "isCreateFolder",
          value: false
        });
      }
    },
    // 切换到相应的文件夹
    switchTo(item) {
      let index = this.crumbsList.indexOf(item)
      this.$store.commit("SET_PUBLICFILES", []);
      this.crumbsList = this.crumbsList.slice(0, index + 1);
      this.queryFloder(item, "SET_PUBLICFILES");
      this.setPower();
    },
    // 返回上一个文件夹
    goBack() {
      this.crumbsList.pop();
      this.$store.commit("SET_PUBLICFILES", []);
      this.queryFloder(
        this.crumbsList[this.crumbsList.length - 1],
        "SET_PUBLICFILES"
      );
      this.setPower();
    }
  }
};
</script>

<style lang="scss" scoped>
.public-files {
  width: 100%;
  height: 100%;
}
</style>



