<template>
  <div class="download-page">
    <cloud-table
      :tableTitle="tableTitle"
      :tableData="download_list"
      :isProcessBar="true"
      :rowClickColor="'#eaf3ff'"
      :isRename="false"
      :isDownload="false"
      :isDelete="true"
      @row-delete="delItem"
    ></cloud-table>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import CloudTable from "./../common/table/table.vue";
import cloudMixin from "./../common/cloudMixin.js";
export default {
  mixins: [cloudMixin],
  data() {
    return {
      tableTitle: [
        {
          text: this.$t("cloud.fileName"),
          width: "40%",
          class: "table-header",
          sortable: false,
          align: "left",
          field: "fileName",
          txtPos: "start"
        },
        {
          text: this.$t("cloud.size"),
          width: "15%",
          class: "table-header",
          sortable: false,
          align: "left",
          field: "fileSize",
          txtPos: "start"
        },
        {
          text: this.$t("cloud.progress"),
          width: "30%",
          class: "table-header",
          sortable: false,
          align: "center",
          field: "createTime",
          txtPos: "start"
        },
        {
          text: this.$t("cloud.status"),
          width: "15%",
          class: "table-header",
          sortable: false,
          align: "left",
          field: "createTime",
          txtPos: "start"
        }
      ]
    };
  },
  components: {
    CloudTable
  },
  computed: {
    ...mapGetters(["download_list"])
  },
  methods: {
    // 取消下载
    delItem(items, index) {
      let confirmText =
        items.process < 100
          ? this.$t("cloud.cancelTransfering")
          : this.$t("cloud.removeList");
      this.$alert.confirm({
        confirmText: confirmText,
        title: this.$t("cloud.Warning"),
        onConfirm: () => {
          if (items.process < 100) {
            this.cancelFileOpera(items.localId, index, 2, items.fileId);
          } else {
            console.log("items:", items);
            this.deleteLocalFile(items);
            this.$store.commit("SET_DOWNLOADLIST", {
              flag: 2,
              index: index,
              fileId: items.fileId
            });
          }
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.download-page {
  width: 100%;
}
</style>



