import Vue from 'vue'
import Router from 'vue-router'
import MainPage from '@/components/main-page/index.vue';
import MyFiles from '@/components/myfiles-page/index.vue';
import PublicFiles from '@/components/public-page/index.vue';
import ShareFiles from '@/components/sharefiles-page/index.vue';
import Download from '@/components/download-page/index.vue';
import Upload from '@/components/upload-page/index.vue'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/MainPage',
    name: 'MainPage',
    component: MainPage,
    redirect: '/MyFiles',
    'children': [{
        path: '/MyFiles',
        name: 'MyFiles',
        component: MyFiles
      },
      {
        path: '/PublicFiles',
        name: 'PublicFiles',
        component: PublicFiles
      },
      {
        path: '/ShareFiles',
        name: 'ShareFiles',
        component: ShareFiles
      },
      {
        path: '/Download',
        name: 'Download',
        component: Download
      },
      {
        path: '/Upload',
        name: 'Upload',
        component: Upload
      }
    ]
  }, {
    path: '/',
    redirect: {
      name: 'MyFiles'
    }
  }]
})



