// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import store from './store'
import router from './router'
import i18n from './assets/common/i18n'
// 加载vuetify框架
import Vuetify from 'vuetify'
// 加载vuetify样式
import 'vuetify/dist/vuetify.min.css'
// 加载字体库
import 'material-design-icons-iconfont/dist/material-design-icons.css'
import '@mdi/font/css/materialdesignicons.min.css'
// 加载animate动画库
import 'animate.css/animate.min.css'

// // import moment from 'moment/moment'
// import iconfont from './../static/iconfont/iconfont.css'
import cdui from './components/common/index.js'
import navbarMethods from './components/navbar/index.vue'
Vue.config.productionTip = false
import './mock/client'


let service = null;
let dbPath = '';
let agent = navigator.userAgent.toLowerCase();
if (agent.indexOf('electron') !== -1) {
  if (!window.require) {
    window.require = window.nodeRequire;
  }
  service = window.require('electron').remote.getGlobal('service');
  dbPath = service.config.userFilePath() + 'cloudData/';
  service.file.fileOperate({filepath: dbPath +'cache', type: 1});
  Vue.prototype.$shell = window.require('electron').shell;
  i18n.locale = service.config.getLanguage();
  console.log('i18n.locale:', i18n.locale);
}

Vue.prototype.$signalMonitor = new Vue()
Vue.prototype.$uploadFile = navbarMethods.uploadFile;

// 时间过滤器
// Vue.filter('moment', function (value, formatString) {
//   formatString = formatString || 'YYYY-MM-DD HH:mm:ss';
//   return moment.unix(value).format(formatString);
// });
Vue.use(cdui);
Vue.use(navbarMethods);
Vue.use(Vuetify);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  i18n,
  components: {
    App
  },
  template: '<App/>'
})



