import Vue from 'vue';
import _ from 'lodash';
import i18n from '../../assets/common/i18n'

function HtmlDecode(str) {
  var t = document.createElement("div");
  t.innerHTML = str;
  return t.textContent || t.innerText;
}
const clouDdist = {
  state: {
    // 用户账号信息
    account: {},
    /**
     * 当前文件夹信息
     */
    currentFolderInfo: {},
    /**
     * 我的文件列表
     */
    myFiles: [],
    /**
     * 公共文件列表
     */
    publicFiles: [],
    /**
     * 共享文件列表
     */
    shareFiles: [],
    /**
     * 搜索结果列表
     */
    searchList: [],
    /**
     * 正在上传的列表
     */
    uploadList: [],
    /**
     * 正在下载的列表
     */
    downloadList: [],
    /**
     * 授权人员列表
     */
    powerMemberList: [],
    /**
     * 操作权限配置
     */
    powerOption: {
      isUpload: true,
      isDownload: true,
      isDelete: true,
      isRename: true,
      isCreateFolder: true,
      showSearchButton: true,
      isSearching: false,
      createFolderTxt: i18n.t('cloud.newFolde'),
      isLoading: false,
      showMoveModal: false,
      moveFileInfo: null,
    },
    /*
     * 组织列表
     */
    organizationList: [],
    /**
     * 组织架构结点
     */
    orgIdList: [],
    /**
     * 弹出框显示状态
     */
    dialog: false,
    /**
     * 成员权限弹出框显示状态
     */
    powerModal: false,
  },
  mutations: {
    SET_ACCOUNT: (state, data) => {
      state.account = data
    },
    SET_POWEROPTION: (state, data) => {
      Vue.set(state.powerOption, data.key, data.value)
    },
    SET_CUURENTFOLDERINFO: (state, data) => {
      state.currentFolderInfo = data;
    },
    SET_ORGANIZATON_LIST: (state, organization) => {
      console.log(organization, 'in vuex organization');
      if (organization === 0) {
        state.orgIdList = [];
        state.organizationList = []
      } else {
        state.organizationList = organization;
      }
    },
    SET_MYFIES: (state, data) => {
      switch (data.flag) {
        // 删除文件
        case 0: {
          // Vue.delete(state.myFiles, data.val);
          state.myFiles.map((item, index) => {
            if (item.fileId === data.val) Vue.delete(state.myFiles, index);
          })
          break
        }
        // 添加文件
        case 1: {
          console.log(data.val, 'in vuex add file info');
          data.val.fileName = HtmlDecode(data.val.fileName);
          data.val.fileSuffix = HtmlDecode(data.val.fileSuffix);
          state.myFiles.push(data.val);
          break
        }
        // 重命名文件
        case 2: {
          console.log(data, 'in vuex reset file name');
          state.myFiles.some((item, index) => {
            if (item.fileId === data.fileId) {
              console.log("item:::",item)
              let tempObj = Object.assign({}, state.myFiles[index]);
              tempObj.fileName = data.val;
              tempObj.updateTime = data.updateTime;
              Vue.set(state.myFiles, index, tempObj);
              return true;
            }
          })
          break
        }
        // 文件下载成功后插入文件路径
        case 3: {
          console.log(data, 'in vuex reset file name');
          state.myFiles.some((item, index) => {
            if (item.fileId === data.fileId) {
              let tempObj = Object.assign({}, state.myFiles[index]);
              tempObj.fileLocalPath = data.val;
              Vue.set(state.myFiles, index, tempObj);
              return true;
            }
          })
          break
        }
        default: {
          data.map((item, index) => {
            item.fileName = HtmlDecode(item.fileName);
            item.fileSuffix = HtmlDecode(item.fileSuffix);
          })
          state.myFiles = data;
          break;
        }
      }
    },
    SET_PUBLICFILES: (state, data) => {
      switch (data.flag) {
        // 删除文件
        case 0: {
          state.publicFiles.map((item, index) => {
            if (item.fileId === data.val) Vue.delete(state.publicFiles, index);
          })
          break
        }
        // 添加文件
        case 1: {
          console.log(data.val, 'in vuex add file info');
          data.val.fileName = HtmlDecode(data.val.fileName);
          data.val.fileSuffix = HtmlDecode(data.val.fileSuffix);
          state.publicFiles.push(data.val);
          break
        }
        // 重命名文件
        case 2: {
          console.log(data, 'in vuex reset file name');
          state.publicFiles.some((item, index) => {
            if (item.fileId === data.fileId) {
              let tempObj = Object.assign({}, state.publicFiles[index]);
              tempObj.fileName = data.val;
              tempObj.updateTime = data.updateTime;
              Vue.set(state.publicFiles, index, tempObj);
              return true;
            }
          })
          break
        }
        // 文件下载成功后插入文件路径
        case 3: {
          console.log(data, 'in vuex reset file name');
          state.publicFiles.some((item, index) => {
            if (item.fileId === data.fileId) {
              let tempObj = Object.assign({}, state.publicFiles[index]);
              tempObj.fileLocalPath = data.val;
              Vue.set(state.publicFiles, index, tempObj);
              return true;
            }
          })
          break
        }
        default: {
          data.map((item, index) => {
            item.fileName = HtmlDecode(item.fileName);
            item.fileSuffix = HtmlDecode(item.fileSuffix);
          })
          state.publicFiles = data;
          break;
        }
      }
    },
    SET_SHAREFIES: (state, data) => {
      switch (data.flag) {
        case 0: {
          // Vue.delete(state.shareFiles, data.val);
          state.shareFiles.map((item, index) => {
            if (item.fileId === data.val) Vue.delete(state.shareFiles, index);
          })
          break
        }
        case 1: {
          console.log(data.val, 'in vuex add file info');
          data.val.fileName = HtmlDecode(data.val.fileName);
          data.val.fileSuffix = HtmlDecode(data.val.fileSuffix);
          state.shareFiles.push(data.val);
          break
        }
        case 2: {
          console.log(data, 'in vuex reset file name');
          state.shareFiles.some((item, index) => {
            if (item.fileId === data.fileId) {
              let tempObj = Object.assign({}, state.shareFiles[index]);
              tempObj.fileName = data.val;
              tempObj.updateTime = data.updateTime;
              Vue.set(state.shareFiles, index, tempObj);
              return true;
            }
          })
          break
        }
        // 文件下载成功后插入文件路径
        case 3: {
          // console.log(data, 'in vuex reset file name');
          state.shareFiles.some((item, index) => {
            if (item.fileId === data.fileId) {
              let tempObj = Object.assign({}, state.shareFiles[index]);
              tempObj.fileLocalPath = data.val;
              Vue.set(state.shareFiles, index, tempObj);
              return true;
            }
          })
          break;
        }
        default: {
          data.map((item, index) => {
            item.fileName = HtmlDecode(item.fileName);
            item.fileSuffix = HtmlDecode(item.fileSuffix);
          })
          state.shareFiles = data;
          break;
        }
      }
    },
    SET_SEARCHLIST: (state, data) => {
      // if (data.flag === 0) {
      //   Vue.delete(state.searchList, data.val);
      // } else {
      //   state.searchList = data.val;
      // }
      switch (data.flag) {
        case 0: {
          // Vue.delete(state.searchList, data.val);
          state.searchList.map((item, index) => {
            if (item.fileId === data.val) Vue.delete(state.searchList, index);
          })
          break
        }
        case 1: {
          data.val.map((item, index) => {
            item.fileName = HtmlDecode(item.fileName);
            item.fileSuffix = HtmlDecode(item.fileSuffix);
          })
          state.searchList = data.val;
          break
        }
        case 2: {
          console.log(data, 'in vuex reset file name');
          state.searchList.some((item, index) => {
            if (item.fileId === data.fileId) {
              let tempObj = Object.assign({}, state.searchList[index]);
              tempObj.fileName = data.val;
              tempObj.updateTime = data.updateTime;
              Vue.set(state.searchList, index, tempObj);
              return true;
            }
          })
          break
        }
        // 文件下载成功后插入文件路径
        case 3: {
          // console.log(data, 'in vuex reset file name');
          state.searchList.some((item, index) => {
            if (item.fileId === data.fileId) {
              let tempObj = Object.assign({}, state.searchList[index]);
              tempObj.fileLocalPath = data.val;
              Vue.set(state.searchList, index, tempObj);
              return true;
            }
          })
          break;
        }
        default: {
          state.shareFiles = data;
          break;
        }
      }
    },
    SET_UPLOADLIST: (state, data) => {
      switch (data.flag) {
        case 0: {
          data.item.fileName = HtmlDecode(data.item.fileName);
          data.item.fileSuffix = HtmlDecode(data.item.fileSuffix);
          state.uploadList.push(data.item)
          break
        }
        case 1: {
          state.uploadList.some((item, index) => {
            if (item.localId === data.localId) {
              state.uploadList[index].process = data.process;
              return true;
            }
          })
          break
        }
        case 2: {
          state.uploadList.some((item, index) => {
            if (item.localId === data.localId) {
              Vue.delete(state.uploadList, index);
              return true;
            }
          })
          break
        }
        case 3: { //上传结果记录
          state.uploadList.some((item, index) => {
            if (item.localId === data.localId) {
              if (data.uploadFail) {
                state.uploadList[index].uploadFail = true;
              } else {
                delete state.uploadList[index].uploadFail
              }
              return true;
            }
          })
          break
        }
      }
    },
    SET_DOWNLOADLIST: (state, data) => {
      switch (data.flag) {
        case 0: {
          data.item.fileName = HtmlDecode(data.item.fileName);
          data.item.fileSuffix = HtmlDecode(data.item.fileSuffix);
          state.downloadList.push(data.item)
          break
        }
        case 1: {
          state.downloadList.some((item, index) => {
            if (item.fileId === data.fileId) {
              state.downloadList[index].process = data.process;
              return true;
            }
          })
          break
        }
        case 2: {
          state.downloadList.some((item, index) => {
            if (item.fileId === data.fileId) {
              Vue.delete(state.downloadList, index);
              return true;
            }
          })
          break
        }
      }
    },
    SET_POWERMEMBERlIST: (state, data) => {
      state.powerMemberList = data;
    },
    SET_DIALOG: (state, data) => {
      state.dialog = data
    },
    SET_POWERMODAL: (state, data) => {
      state.powerModal = data
    }
  }
};

export default clouDdist;



