<template>
  <v-list class="pt-0" dense>
    <v-list-tile
      v-for="(item, index) in dataList"
      :key="index"
      @click="selsctItem(item, index)"
      class="pr-0 pl-0"
      avatar
      :style="{pointerEvents: item.isInclude?'none':'auto'}"
    >
      <v-list-tile-action :style="{'min-width':'32px'}" v-if="isCheckItem">
        <input type="checkbox" :checked="item.isChecked">
      </v-list-tile-action>
      <v-list-tile-avatar>
        <img @error="defaultIcon" :src="item.thumbAvatar || item.headImg || item.userHead" alt>
      </v-list-tile-avatar>
      <v-list-tile-content>
        <v-list-tile-title>{{item.name || item.userName || item.enName}}</v-list-tile-title>
      </v-list-tile-content>
      <v-list-tile-action v-if="isOption" :style="{'flex-direction':'row'}">
        <v-btn color="info" small @click="changePwore(item, index)">{{$t('cloud.permissions')}}</v-btn>
        <v-btn color="error" small @click="delMember(item, index)" class="ml-2">{{$t('cloud.delete')}}</v-btn>
      </v-list-tile-action>
    </v-list-tile>
  </v-list>
</template>

<script>
import { mapGetters } from "vuex";
import cloudMixin from "./../common/cloudMixin.js";
export default {
  mixins: [cloudMixin],
  data() {
    return {
      icon: require("./../../assets/img/moren.jpg")
    };
  },
  props: {
    dataList: Array | Object,
    isOption: {
      type: Boolean,
      default: true
    },
    isCheckItem: {
      type: Boolean,
      default: false
    }
  },
  components: {},
  computed: {
    ...mapGetters([
      "user_account",
      "share_files",
      "search_list",
      "power_option"
    ])
  },
  created() {},
  mounted() {},
  methods: {
    selsctItem(item, index) {
      this.$emit("selsct-item", item, index);
    },
    changePwore(item, index) {
      this.$emit("change-pwore", item, index);
    },
    delMember(item, index) {
      this.$emit("del-member", item, index);
    },
    defaultIcon(e) {
      e.target.src = this.icon;
    }
  }
};
</script>

<style lang="scss" scoped>
</style>



