<template>
  <div class="upload-page">
    <cloud-table
      :tableTitle="tableTitle"
      :tableData="upload_list"
      :isProcessBar="true"
      :isRename="false"
      :isDownload="false"
      :isDelete="true"
      :rowClickColor="'#eaf3ff'"
      @row-delete="delItem"
    ></cloud-table>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import CloudTable from "./../common/table/table.vue";
import cloudMixin from "./../common/cloudMixin.js";
export default {
  mixins: [cloudMixin],
  data() {
    return {
      tableTitle: [
        {
          text: this.$t("cloud.fileName"),
          width: "40%",
          class: "table-header",
          sortable: false,
          align: "left",
          field: "fileName",
          txtPos: "start"
        },
        {
          text: this.$t("cloud.size"),
          width: "15%",
          class: "table-header",
          sortable: false,
          align: "left",
          field: "fileSize",
          txtPos: "start"
        },
        {
          text: this.$t("cloud.progress"),
          width: "30%",
          class: "table-header",
          sortable: false,
          align: "center",
          field: "createTime",
          txtPos: "start"
        },
        {
          text: this.$t("cloud.status"),
          width: "15%",
          class: "table-header",
          sortable: false,
          align: "left",
          field: "createTime",
          txtPos: "start"
        }
      ]
    };
  },
  components: {
    CloudTable
  },
  computed: {
    ...mapGetters(["upload_list", "search_list"])
  },
  methods: {
    // 删除选中的条目
    delItem(items, index) {
      let confirmText =
        items.process < 100 ? this.$t("cloud.uploadingCancel") : this.$t("cloud.remove");
      console.log("删除的条目:", items, index);
      this.$alert.confirm({
        confirmText: confirmText,
        title: this.$t("cloud.Warning"),
        onConfirm: () => {
          if (items.process < 100) {
            this.cancelFileOpera(items.localId, index, 1, items.fileId);
          } else {
            this.$store.commit("SET_UPLOADLIST", {
              flag: 2,
              index: index,
              localId: items.localId
            });
          }
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.upload-page {
  width: 100%;
}
</style>



