<template>
  <div class="org-item">
    <ul v-if="organization_List.length" class="node-ul" v-for="(value, key, index) in organization_List" :key="key">
      <org-node
        :model="value"
        :index="index"
        :folderData="folderData"
        :hasMemberList="hasMemberList"
        v-if="value"
        @org-selected-users="orgSelectedUsers"
      ></org-node>
    </ul>
  </div>
</template>
<script>
import OrgNode from './org-node.vue';
import { mapGetters } from 'vuex';
const orgData = require('./orgData.json');
export default {
  data () {
    return {
      organizationList: orgData
    }
  },
  props: {
    folderData: Object,
    hasMemberList: Array
  },
  components: {
    OrgNode
  },
  computed: {
    ...mapGetters(['organization_List', 'orgId_List'])
    // isReGetOrganization () {
    //   return this.organization_List.length === 0;
    // }
  },
  watch: {
    // isReGetOrganization(val) {
    //   if (val) this.setOrganizationList()
    // }
  },
  methods: {
    orgSelectedUsers(item) {
      this.$emit('org-selected-users', item)
    }
    // // 获取组织架构数据
    // setOrganizationList() {
    //   let getVisibleOrgUsersParam = { type: 0, orgId: 0 };
    //   this.$Service.enterprise.getVisibleOrgUsers(getVisibleOrgUsersParam, resp => {
    //     console.log('组织架构信息', resp);
    //     if (resp.code !== 0) return;
    //     this.$set(this.orgId_List, 0, 0);
    //     this.$store.commit('SET_ORGANIZATON_LIST', resp.orgs);
    //   });
    // }
  }
};
</script>
<style lang="scss">
.org-item {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  ul {
    width: 100%;
    overflow: hidden;
    margin: 0;
    padding: 0;
    &:hover {
      overflow-y: auto;
    }
  }
  .node-ul {
    margin-top: 0;
    list-style: none;
  }
  .defalut-page {
    width: 100%;
    height: 100%;
  }
}
</style>



