<template>
  <v-layout row justify-center>
    <v-dialog
      v-model="power_option.showMoveModal"
      class="moveModal-dialog"
      scrollable
      max-width="300px"
    >
      <v-card>
        <v-card-title class="pt-2 pb-2 pl-1 pr-1">
          <v-icon
            :class="{'non-click': !folderList.length}"
            class="ml-2"
            :title="$t('cloud.backToHigherLevel')"
            @click="backToHigherLevel"
          >keyboard_return</v-icon>
          <i class="iconfont icon-anjianfengexian fg-icon"></i>
          <a class="root-folde" @click="folderList=[];breadList=[]">{{$t('cloud.home')}}</a>
          <div class="hidden-folde" v-if="showHiddenFolder">
            <span class="xiang-you">></span>
            <span class="title" @click="switchToHidden()">······</span>
          </div>
          <v-breadcrumbs :items="currentBreadList" class="pt-0 pb-0">
            <template v-slot:item="props">
              <a
                @click.prevent="switchTo(props.item)"
                :title="props.item.fileName"
              >{{HtmlDecode(props.item.fileName)}}</a>
            </template>
          </v-breadcrumbs>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text style="height: 300px;" class="pr-0 pl-0 pt-0">
          <v-list subheader dense>
            <v-list-tile
              v-for="item in currentFolderList"
              :key="item.title"
              avatar
              @click="selectFolder = item"
              @dblclick="selectFolder = item;queryFolder(1,item)"
              :class="{'file-item-bg': selectFolder.fileId === item.fileId}"
            >
              <v-list-tile-avatar :style="{minWidth:'40px'}">
                <v-icon color="yellow darken-2">{{ item.icon }}</v-icon>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>{{ item.fileName }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions :style="{'height':'44px','justifyContent':'space-between'}">
          <transition
            name="custom-classes-transition"
            enter-active-class="animated fadeIn"
            leave-active-class="animated fadeOut"
          >
            <v-btn
              v-show="breadList.length"
              color="success"
              @click="createNewFolder()"
              small
              key="create"
            >{{$t('cloud.newFolde')}}</v-btn>
          </transition>
          <transition
            name="custom-classes-transition"
            enter-active-class="animated fadeIn"
            leave-active-class="animated fadeOut"
          >
            <v-btn
              v-show="canMoveTo"
              color="info"
              @click="moveToFolde"
              small
              key="move"
            >{{$t('cloud.OK')}}</v-btn>
          </transition>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
import { mapGetters } from "vuex";
import _ from "lodash";
import BreadCrumbs from "./bread-crumbs.vue";
export default {
  data() {
    return {
      selectFolder: { fileId: "0" }, //当前选择的文件夹
      rootFolderList: [
        {
          icon: "folder_shared",
          fileName: this.$t("cloud.myFile"),
          fileId: "1",
          fileType: 2,
          fileCode: "10011001"
        },
        {
          icon: "folder",
          fileName: this.$t("cloud.publicFile"),
          fileId: "3",
          fileType: 4,
          fileCode: "10011003"
        },
        {
          icon: "share",
          fileName: this.$t("cloud.sharedFile"),
          fileId: "2",
          fileType: 3,
          fileCode: "10011002"
        }
      ],
      folderList: [], //文件夹列表
      breadList: [], //导航栏列表
      showHiddenFolder: false, //是否显示省略号
      loading: false,
      canMoveTo: false,
      isShow: true,
      dialogm1: ""
    };
  },
  props: {},
  watch: {
    breadList(val) {
      this.selectFolder = this.breadList.length
        ? this.breadList[this.breadList.length - 1]
        : { fileId: "0" };
      console.log("this.selectFolder:", this.selectFolder);
      if (!this.breadList.length) {
        this.canMoveTo = false;
        return;
      }
      if (this.breadList.length === 1) {
        if (this.breadList[0].fileId === "1") {
          this.canMoveTo = true;
          return;
        } else {
          if (this.power_option.moveFileInfo.fileType === 1) {
            this.canMoveTo = false;
            return;
          } else {
            this.canMoveTo = true;
            return;
          }
        }
      }
      this.canMoveTo = true;
      return;
    }
  },
  components: {},
  computed: {
    ...mapGetters(["power_option", "user_account"]),
    currentFolderList() {
      return this.folderList.length || this.breadList.length
        ? this.folderList
        : this.rootFolderList;
    },
    currentBreadList() {
      if (
        this.breadList &&
        this.breadList.length &&
        this.breadList.length > 2
      ) {
        this.showHiddenFolder = true;
        return this.breadList.slice(-2);
      } else {
        this.showHiddenFolder = false;
        return this.breadList;
      }
    }
  },
  created() {},
  methods: {
    HtmlDecode(str) {
      var t = document.createElement("div");
      t.innerHTML = str;
      return t.textContent || t.innerText;
    },
    queryFolder(type, item) {
      if (type === 1) {
        this.breadList.push(item);
      }
      console.log("type:", type, "item:", item);
      this.folderList = [];
      this.loading = true;
      this.$service.cloud.queryFloder(
        { userId: this.user_account.id, pid: item.fileId },
        resp => {
          console.log("resp:", resp);
          this.loading = false;
          resp.cloudFileInfo.forEach(element => {
            element.fileName = this.HtmlDecode(element.fileName)
            if (
              element.fileType !== 1 &&
              element.fileId !== this.power_option.moveFileInfo.fileId
            ) {
              element.icon = "folder_shared";
              this.fifterFolder(element);
            }
          });
        }
      );
    },
    switchTo(item) {
      console.log("switchTo");
      console.log("item:", item);
      this.breadList.some((element, index) => {
        if (element.fileId === item.fileId) {
          console.log("index:", index);
          this.breadList = this.breadList.slice(0, index + 1);
          return true;
        }
      });
      this.queryFolder(2, item);
    },
    switchToHidden() {
      this.breadList.splice(this.breadList.length - 2, 2);
      this.queryFolder(2, this.breadList[this.breadList.length - 1]);
    },
    backToHigherLevel() {
      console.log("backToHigherLevel");
      this.breadList.splice(this.breadList.length - 1, 1);
      if (this.breadList.length > 0) {
        this.queryFolder(2, this.breadList[this.breadList.length - 1]);
      } else {
        this.folderList = [];
      }
    },
    closeModel() {
      this.$store.commit("SET_POWEROPTION", {
        key: "showMoveModal",
        value: false
      });
      this.$store.commit("SET_POWEROPTION", {
        key: "moveFileInfo",
        value: null
      });
    },
    createNewFolder() {
      this.$alert.prompt({
        // title: '创建文件夹',this.$t('cloud.createFload')
        title: this.$t("cloud.createFload"),
        inputAttrs: {
          placeholder: this.$t("cloud.folderName"),
          maxlength: 24
        },
        onConfirm: value => {
          if (
            this.folderList.some((item, index) => {
              if (item.fileName === value) {
                return true;
              }
            })
          ) {
            //有重名的文件夹
            this.$toast.warning({ message: this.$t("cloud.createDirFail") });
            console.log("有重名的文件夹");
            return;
          }
          let createFloderParam = {
            pid: this.breadList[this.breadList.length - 1].fileId, // 云盘父目录id 如果没有父目录，我的文件传'1'，共享文件传'2'。*
            ownerId: this.user_account.id, // 拥有者id* userid
            uploaderName: this.user_account.name, // 上传者名字*
            uploaderId: this.user_account.id, // 上传者id* userid
            encryptFileSize: "", // 加密文件大小
            fileId: "", // 文件ID(默认不填值)
            fileCode: this.breadList[this.breadList.length - 1].fileCode, // 父节点的fileCode，我的文件跟目录传'10011001'，共享文件根目录传'10011002'*
            fileName: value, // 文件名*
            filePath: "", // 文件路径
            fileRemark: "", // 文件备注
            fileClass: "", // 文件分类
            fileSize: "", // 文件大小
            fileType: this.breadList[0].fileType, // 类型 1.文件  2.文件夹  3.团队文件夹  4 特殊文件夹（我的文件2，共享文件传3）*
            md5Hash: "", // md5码
            encryptKey: "" // 加密key
          };
          switch (this.breadList[0].fileId) {
            case "1":
              this.$service.cloud.addFile(createFloderParam, resp => {
                if (resp.code === 0) {
                  this.queryFolder(
                    2,
                    this.breadList[this.breadList.length - 1]
                  );
                } else {
                  this.$toast.error({
                    message: this.$t("cloud.createFail", { value: resp.code })
                  });
                }
              });
              break;
            case "2":
              this.$service.cloud.createTeam(createFloderParam, resp => {
                if (resp.code === 0) {
                  this.queryFolder(
                    2,
                    this.breadList[this.breadList.length - 1]
                  );
                } else {
                  this.$toast.error({
                    message: this.$t("cloud.createFail", { value: resp.code })
                  });
                }
              });
              break;
            case "3":
              this.$service.cloud.createPubDir(createFloderParam, resp => {
                if (resp.code === 0) {
                  this.queryFolder(
                    2,
                    this.breadList[this.breadList.length - 1]
                  );
                } else {
                  this.$toast.error({
                    message: this.$t("cloud.createFail", { value: resp.code })
                  });
                }
              });
              break;
            default:
              break;
          }
        }
      });
    },
    moveToFolde() {
      if (this.selectFolder.fileId === "0") return;
      if (
        this.currentFolderList.some((item, index) => {
          console.log("item:", item);
          if (item.fileId === this.selectFolder.fileId) return true;
        }) ||
        this.breadList[this.breadList.length - 1].fileId ===
          this.selectFolder.fileId
      ) {
        this.$alert.prompt({
          confirmText: this.$t("cloud.moveToTarget", {
            fileName: this.power_option.moveFileInfo.fileName,
            targetFileName: this.HtmlDecode(this.selectFolder.fileName)
          }),
          title: this.$t("cloud.prompt"),
          onConfirm: () => {
            let param = {
              userId: this.user_account.id,
              dstFolderId: this.selectFolder.fileId,
              fileIds: [this.power_option.moveFileInfo.fileId]
            };
            console.log("param:", param);
            this.$service.cloud.moveFile(param, resp => {
              console.log("moveFileResp:", resp);
              if (resp.code === 0) {
                this.$emit("refreshData"); // 刷新界面数据
                this.closeModel();
              } else {
                this.$toast.error({
                  message: this.$t("cloud.moveFail", { value: resp.code })
                });
              }
            });
          }
        });
      }
    },
    fifterFolder(element) {
      //根据权限过滤文件夹
      if (this.breadList[0].fileId === "1") {
        //我的文件夹下随便整
        this.folderList.push(element);
      } else if (this.breadList[0].fileId === "2") {
        //团队文件夹 判断是否有上传权限才可以进行移入
        this.$service.cloud.getPermission(
          {
            roleId: element.roleId,
            userId: this.user_account.id,
            fileId: element.fileId
          },
          resp => {
            console.log("resp:", resp);
            if (resp.isUpload === 1) {
              this.folderList.push(element);
            }
          }
        );
      } else if (this.breadList[0].fileId === "3") {
        //公开文件夹下只有自己的文件夹才能操作
        if (this.user_account.id === element.ownerId) {
          this.folderList.push(element);
        }
      }
    },
    HtmlDecode(str) {
      var t = document.createElement("div");
      t.innerHTML = str;
      return t.textContent || t.innerText;
    }
  }
};
</script>

<style lang="scss" scoped>
.file-item-bg {
  background-color: rgb(235, 234, 233);
}
</style>



