<template>
  <div class="share-files">
    <bread-crumbs :crumbsList="crumbsList" @switch-to="switchTo" @go-back="goBack"></bread-crumbs>
    <cloud-table
      :tableData="tableData"
      :tableTitle="tableTitle"
      :rowClickColor="'#eaf3ff'"
      :isRename="power_option.isRename"
      :isDownload="power_option.isDownload"
      :isDelete="power_option.isDelete"
      :yunFileType="'SET_SHAREFIES'"
      @row-dbclick="openFolder"
      @row-delete="delShareFile"
      @power-admin="powerAdmin"
      :tableType="3"
      :style="{position: 'absolute',top: '41px',left: '0px'}"
    ></cloud-table>
    <power-modal v-if="powerModal_state" :folderData="folderData"></power-modal>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import CloudTable from "./../common/table/table.vue";
import cloudMixin from "./../common/cloudMixin.js";
import BreadCrumbs from "./../common/other/bread-crumbs.vue";
import PowerModal from "./../sharefiles-page/modal.vue";
export default {
  mixins: [cloudMixin],
  data() {
    return {
      crumbsList: [
        { text: this.$t("cloud.allFiles"), fileId: "2", fileCode: "10011002" }
      ],
      isPowerModal: false,
      folderData: {}
    };
  },
  components: {
    CloudTable,
    BreadCrumbs,
    PowerModal
  },
  computed: {
    ...mapGetters([
      "user_account",
      "share_files",
      "search_list",
      "power_option",
      "current_folderInfo",
      "powerModal_state"
    ]),
    tableData() {
      if (!this.power_option.isSearching) {
        return this.share_files;
      } else {
        return this.search_list;
      }
    }
  },
  created() {
    // 获取共享文件根目录内容
    this.getRootFolder();
    this.$store.commit("SET_CUURENTFOLDERINFO", {});
    this.$store.commit("SET_CUURENTFOLDERINFO", this.crumbsList[0]);
  },
  methods: {
    // 关闭重置角色弹框
    closeModal() {
      this.isPowerModal = false;
    },
    // 获取共享文件根目录内容
    getRootFolder() {
      this.getTeams();
    },
    // 打开文件夹
    openFolder(items, index) {
      this.crumbsList = this.noRepeatCrumbs(items, this.crumbsList);
      this.power_option.isUpload = true;
      this.$store.commit("SET_SHAREFIES", []);
      this.queryFloder(items, "SET_SHAREFIES");
    },
    // 删除文件
    delShareFile(items, index) {
      this.delFile(items, index, "SET_SHAREFIES");
    },
    // 管理员权限
    powerAdmin(items, index) {
      console.log(items, index);
      this.$store.commit("SET_POWERMODAL", true);
      this.folderData = items;
    },
    // 面包屑导航切换
    switchTo(item) {
      this.$store.commit("SET_SHAREFIES", []);
      let index = this.crumbsList.indexOf(item);
      if (index === 0) {
        this.$store.commit("SET_POWEROPTION", {
          key: "isUpload",
          value: false
        });
        this.$store.commit("SET_POWEROPTION", {
          key: "isCreateFolder",
          value: true
        });
        this.getRootFolder();
        this.crumbsList = this.crumbsList.slice(0, 1);
      } else {
        this.crumbsList = this.crumbsList.slice(0, index + 1);
        this.queryFloder(item, "SET_SHAREFIES");
      }
    },
    // 面包屑导航返回父文件夹
    goBack() {
      this.crumbsList.pop();
      this.$store.commit("SET_SHAREFIES", []);
      if (this.crumbsList.length === 1) {
        this.$store.commit("SET_POWEROPTION", {
          key: "isUpload",
          value: false
        });
        this.$store.commit("SET_POWEROPTION", {
          key: "isCreateFolder",
          value: true
        });
        this.getRootFolder();
      } else {
        this.queryFloder(
          this.crumbsList[this.crumbsList.length - 1],
          "SET_SHAREFIES"
        );
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.share-files {
  width: 100%;
  height: 100%;
}
</style>



