<template>
  <div>
    <router-view/>
  </div>
</template>

<script>
import Vue from 'vue'
export default {
  name: "App",
  created() {
    console.log("get imsdkservice: ", this.$service);
    console.log("i get env: ", process.env.NODE_ENV);
    let dbPath = this.$service.config.userFilePath(null, resp => {
      dbPath = "";
      dbPath = resp.path + "cloudData/";
      Vue.prototype.$dbPath = dbPath
    }).path;
    dbPath += "cloudData/";
    //  如果是开发模式，需要登录一次
    if (process.env.NODE_ENV === "development") {
      let param = {
        user: "008618791864382",
        pwd: "hx970501",
        userType: 1,
        server: "vrv",
        nation: "0086"
      };
      this.$service.auth.login(param, resp => {
        console.log("i get login result: ", resp);
        if (resp.code === 0) {
          this.$router.push({ path: "/MainPage" });
        }
      });
    } else {
      Vue.prototype.$dbPath = dbPath
    }
    // let agent = navigator.userAgent.toLowerCase();
    // if (agent.indexOf("electron") !== -1) {
    //   // 获取账号信息
    //   this.$service.user.getAccountInfo(null, resp => {
    //     console.log(resp, "获取账户信息回调");
    //     this.$store.commit("SET_ACCOUNT", resp);
    //     this.$router.push({ path: "/MainPage" });
    //   });
    // } else {
    //   this.$router.push({ path: "/MainPage" });
    // }
  },
  beforeDestroy() {
    this.$service.file.fileOperate({
      filepath: this.$dbPath + "cache",
      type: 2
    });
  },
  mounted() {
    document.addEventListener(
      "dragenter",
      function(event) {
        event.preventDefault();
      },
      false
    );
    document.addEventListener(
      "dragover",
      function(event) {
        event.preventDefault();
      },
      false
    );
    document.addEventListener(
      "drop",
      function(event) {
        event.preventDefault();
      },
      false
    );
    document.addEventListener(
      "dragend",
      function(event) {
        event.preventDefault();
      },
      false
    );
  }
};
</script>

<style>
@import "./style/public.css";
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  color: #424e67;
  height: 100%;
  overflow-x: hidden;
}
</style>



