<template>
  <v-dialog :value="showAlert" class="alert-dialog" max-width="290" persistent>
    <v-card>
      <v-card-title
        class="headline lighten-1 white--text"
        :class="type==='confirm'?'red':'blue'"
      >{{title}}</v-card-title>
      <v-card-text>
        <v-text-field
          v-bind="inputAttrs"
          :rules="rules"
          v-model="inputValue"
          single-line
          counter
          clearable
          v-if="type==='prompt'"
          type="text"
          @keyup.enter="confirm"
          autofocus
        ></v-text-field>
        <v-input v-else-if="type==='confirm'" class="title">{{confirmText}}</v-input>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          :color="type==='confirm'?'error':'success'"
          @click="confirm"
          :disabled="!isProper"
        >{{ok}}</v-btn>
        <v-btn color="primary" @click="cancel">{{cancels}}</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script>
import i18n from "../../../assets/common/i18n";
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      showAlert: false,
      inputValue: "",
      ok: i18n.t("cloud.OK"),
      cancels: i18n.t("cloud.cancel"),
      rules: [this.rule],
      flag: true
    };
  },
  props: {
    title: {
      type: String,
      default: "提示"
    },
    inputAttrs: {
      type: Object,
      default: () => {}
    },
    confirmText: {
      type: String,
      default: "确认删除"
    },
    type: String,
    onConfirm: {
      type: Function,
      default: () => {}
    },
    onCancel: {
      type: Function,
      default: () => {}
    }
  },
  computed: {
    isProper() {
      if (this.type === "prompt") {
        if (this.inputValue === null) return false;
        return !this.inputValue.match(/^\s*$/g);
      } else {
        return true;
      }
    }
  },
  created() {
    console.log("i18n", i18n.t("cloud.cancel"));
  },
  methods: {
    rule(val) {
      if (val === null) return true;
      return val.length <= this.inputAttrs.counter;
    },
    close() {
      this.showAlert = false;
      this.flag = true;
    },
    cancel() {
      this.onCancel();
      this.close();
    },
    confirm() {
      if (this.flag && this.showAlert) {
        this.onConfirm(this.inputValue.replace(/(^\s*)|(\s*$)/g, ""));
        this.close();
      }
    }
  },
  beforeMount() {
    document.body.appendChild(this.$el);
  },
  mounted() {
    this.showAlert = true;
  }
};
</script>
<style lang="scss" scoped>

</style>



