<template>
  <v-toolbar dense fixed clipped-left app>
    <v-icon x-large>mdi-cloud-circle</v-icon>
    <v-toolbar-title>{{$t('cloud.cloudName')}}</v-toolbar-title>
    <v-spacer></v-spacer>
    <v-toolbar-items class="hidden-xs-and-down">
      <loacl-search
        :showSearchInput="showSearchInput"
        @show="showSearchInput=true"
        @close="showSearchInput=false"
        v-if="power_option.showSearchButton"
      ></loacl-search>
      <v-btn
        icon
        @click.passive.stop="showSearchInput=true"
        v-if="!showSearchInput"
        :title="$t('cloud.search')"
      >
        <v-icon>search</v-icon>
      </v-btn>
      <v-btn
        icon
        v-if="power_option.isSearching"
        :title="$t('cloud.close')"
        @click="showSearchInput=false"
      >
        <v-icon>close</v-icon>
      </v-btn>
      <v-btn
        icon
        v-if="power_option.showSearchButton&&!power_option.isSearching"
        :title="$t('cloud.refresh')"
        @click="refreshData(flag)"
      >
        <v-icon>refresh</v-icon>
      </v-btn>
    </v-toolbar-items>
  </v-toolbar>
</template>

<script>
import { mapGetters } from "vuex";
import LoaclSearch from "./../common/other/search.vue";
import cloudMixin from "./../common/cloudMixin.js";
export default {
  mixins: [cloudMixin],
  components: {
    LoaclSearch
  },
  data() {
    return {
      showSearchInput: false,
      folderData: {}
    };
  },
  watch: {
    $route() {
      if (this.showSearchInput) this.showSearchInput = false;
    }
  },
  computed: {
    ...mapGetters([
      "my_files",
      "public_files",
      "share_files",
      "search_list",
      "user_account",
      "upload_list",
      "power_option",
      "current_folderInfo"
    ]),
    flag() {
      if (this.$route.name === "MyFiles") return "SET_MYFIES";
      if (this.$route.name === "PublicFiles") return "SET_PUBLICFILES";
      if (this.$route.name === "ShareFiles") return "SET_SHAREFIES";
    }
  },
  created() {
    this.$store.commit("SET_POWEROPTION", {
      key: "createFolderTxt",
      value: this.$t("cloud.newFolde")
    });
  },
  methods: {
    refreshData(flag) {
      let items = {
        fileId: "1",
        ownerId: this.user_account.id
      };
      let tempObj = {};
      console.log(flag, "======");
      if (flag === "SET_MYFIES") {
        if (this.my_files.length === 0) return;
        if (this.my_files[0].pid === "1") {
          this.queryFloder(items, flag);
        } else {
          Object.assign(tempObj, this.my_files[0]);
          tempObj.fileId = this.my_files[0].pid;
          this.queryFloder(tempObj, flag);
        }
      } else if (flag === "SET_SHAREFIES") {
        if (this.share_files.length === 0) return;
        if (this.share_files[0].pid === "2") {
          this.getTeams();
        } else {
          Object.assign(tempObj, this.share_files[0]);
          tempObj.fileId = this.share_files[0].pid;
          this.queryFloder(tempObj, flag);
        }
      } else if (flag === "SET_PUBLICFILES") {
        if (this.public_files.length === 0) return;
        if (this.public_files[0].pid === "3") {
          this.queryFloder({ fileId: "3" }, flag);
        } else {
          Object.assign(tempObj, this.share_files[0]);
          tempObj.fileId = this.public_files[0].pid;
          this.queryFloder(tempObj, flag);
        }
      }
    }
  }
};
</script>

<style lang="scss" scoped>
</style>



