import Vue from 'vue'
import Toast from './toast.vue'

function open(propsData) {
  const ToastComponent = Vue.extend(Toast);
  return new ToastComponent({
    el: document.createElement('div'),
    propsData
  })
}

export default {
  error(params) {
    const defaultParam = {
      type: 'error'
    };
    const propsData = Object.assign(defaultParam, params)
    return open(propsData)
  },
  info(params) {
    const defaultParam = {
      type: 'info'
    };
    const propsData = Object.assign(defaultParam, params)
    return open(propsData)
  },
  warning(params) {
    const defaultParam = {
      type: 'warning'
    };
    const propsData = Object.assign(defaultParam, params)
    return open(propsData)
  },
  loading(params) {
    const defaultParam = {
      type: 'toast-loading'
    };
    const propsData = Object.assign(defaultParam, params)
    return open(propsData)
  },
  closeLoading(params) {

  }
}



