<template>
  <div class="bread-crumbs">
    <transition
      name="custom-classes-transition"
      enter-active-class="animated fadeIn"
      leave-active-class="animated fadeOut"
      mode="out-in"
      :duration="100"
    >
      <div
        v-if="!power_option.isSearching"
        key="bread"
        :style="{display:'flex'}"
        class="bread-crumbs-area"
      >
        <v-btn
          color="info"
          fab
          flat
          depressed
          class="mr-0 ml-1 mt-1 mb-1"
          :style="{width:'34px',height:'34px'}"
          :title="$t('cloud.backToHigherLevel')"
          @click="back"
          :disabled="isBack"
        >
          <v-icon>keyboard_return</v-icon>
        </v-btn>
        <v-breadcrumbs :items="crumbsList" class="pl-2 pr-2 pt-1 pb-1">
          <template v-slot:item="props">
            <span
              class="folder-title"
              @click="switchTo(props.item)"
            >{{props.item.text}}</span>
          </template>
          <template v-slot:divider>
            <v-icon class="ma-0">chevron_right</v-icon>
          </template>
        </v-breadcrumbs>
      </div>
      <!-- 搜索结果 开始 -->
      <div v-if="power_option.isSearching" key="searchList" class="bread-crumbs-area">
        <span class="pl-3 search-text">{{$t('cloud.searchResult',{value:search_list.length})}}</span>
      </div>
      <!-- 搜索结果 结束 -->
    </transition>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  data() {
    return {};
  },
  props: {
    crumbsList: Array
  },
  computed: {
    ...mapGetters(["power_option", "search_list"]),
    isBack() {
      return this.crumbsList.length < 2;
    }
  },
  methods: {
    back() {
      this.$store.commit("SET_SEARCHLIST", { flag: 1, val: [] });
      this.$emit("go-back");
      this.$store.commit(
        "SET_CUURENTFOLDERINFO",
        this.crumbsList[this.crumbsList.length - 1]
      );
    },
    switchTo(item) {
      this.$store.commit("SET_SEARCHLIST", { flag: 1, val: [] });
      this.$emit("switch-to", item);
      this.$store.commit("SET_CUURENTFOLDERINFO", item);
    }
  }
};
</script>

<style lang="scss" scoped>
.bread-crumbs {
  width: 100%;
  height: 41px;
  border-bottom: 1px solid #d1d1d2;
  box-sizing: border-box;
  background-color: #fff;
  position: absolute;
  top: 0px;
  left: 0px;
  .bread-crumbs-area {
    height: 100%;
    position: relative;
    // overflow-x: auto;
    .folder-title {
      color: #1867c0;
      cursor: pointer;
    }
  }
  .search-text{
    line-height: 41px;
  }
}
</style>



