import {
  mapGetters
} from 'vuex';

const mixin = {
  data() {
    return {
      tableTitle: [{
          text: this.$t('cloud.fileName'),
          width: '50%',
          class: 'table-header',
          sortable: false,
          align: 'left',
          field: 'fileName',
          txtPos: 'start'
        },
        {
          text: this.$t('cloud.size'),
          width: '6%',
          class: ['table-header', 'small-th'],
          sortable: false,
          align: 'center',
          field: 'fileSize',
          txtPos: 'start'
        },
        {
          text: this.$t('cloud.createdOn'),
          width: '20%',
          class: 'table-header',
          sortable: false,
          align: 'center',
          field: 'createTime',
          txtPos: 'start'
        },
        {
          text: this.$t('cloud.lastModified'),
          width: '20%',
          class: 'table-header',
          sortable: false,
          align: 'center',
          field: 'updateTime',
          txtPos: 'start'
        }
      ]
    }
  },
  computed: {
    ...mapGetters([
      'user_account',
      'share_files',
      'my_files',
      'search_list',
      'power_option',
      'upload_list',
      'download_list',
    ])
  },
  methods: {
    // 获取本地解密后的文件路径
    getLocalFileDecryptPath(items) {
      return this.$dbPath + 'cache/' + items.fileName + (items.fileSuffix === '' ? '' : '.' + items.fileSuffix)
    },
    // 获取本地源文件路径
    getLocalEncryptFilePath(items) {
      let pathArray = items.filePath.split('/');
      let pathTemp = pathArray[pathArray.length - 1].split('.')[0];
      console.log("本地路径：：：", this.$dbPath + pathTemp)
      return this.$dbPath + pathTemp;
    },
    // 删除本地文件
    deleteLocalFile(items) {
      console.log("item:::", items.fileLocalPath)
      console.log("localPath:::", this.getLocalEncryptFilePath(items))
      if (items.fileLocalPath !== this.getLocalEncryptFilePath(items)) {
        this.$service.file.fileOperate({
          filepath: items.fileLocalPath,
          type: 2
        })
        // 删除上传的本地的文件
      }
      this.$store.commit('SET_DOWNLOADLIST', {
        flag: 2,
        fileId: items.fileId
      })
      this.$service.file.fileOperate({
        filepath: this.getLocalEncryptFilePath(items),
        type: 2
      })
      // 删除加密文件
      this.$service.file.fileOperate({
        filepath: this.getLocalFileDecryptPath(items),
        type: 2
      })
      // 删除解密文件
    },
    // 从后台获取文件信息
    queryFloderFromSdk(items, type, userId) {
      this.$service.cloud.queryFloder({
          pid: items.fileId,
          userId: userId
        },
        resp => {
          console.log(resp, '查询到的文件夹内容');
          this.power_option.isLoading = false;
          if (resp.code === 0) {
            if (type === 'SET_SHAREFIES') this.getMyPermission(items);
            if (type === 'SET_PUBLICFILES' && items.fileId !== '3') {
              if (this.user_account.id === items.ownerId) {
                this.$store.commit('SET_POWEROPTION', {
                  key: 'isUpload',
                  value: true
                });
                this.$store.commit('SET_POWEROPTION', {
                  key: 'isCreateFolder',
                  value: true
                });
              } else {
                this.$store.commit('SET_POWEROPTION', {
                  key: 'isUpload',
                  value: false
                });
                this.$store.commit('SET_POWEROPTION', {
                  key: 'isCreateFolder',
                  value: false
                });
              }
            }
            resp.cloudFileInfo.forEach(element => {
              console.log("element:::", element)
              if (element.fileType === 1) {
                this.$set(element, 'fileLocalPath', '');
                let fileLocalPath = '';
                let hasFile = false;
                if (element.hasOwnProperty('encryptKey') && element.encryptKey) {
                  fileLocalPath = this.getLocalEncryptFilePath(element); //设置源文件本地路径
                  hasFile = this.$service.file.fileOperate({
                    filepath: fileLocalPath,
                    type: 0
                  });
                } else {
                  fileLocalPath = this.getLocalFileDecryptPath(element); //设置源文件本地路径
                  hasFile = this.$service.file.fileOperate({
                    filepath: this.getLocalFileDecryptPath(element),
                    type: 0
                  });
                }
                if (hasFile) element.fileLocalPath = fileLocalPath;
              }
            });
            this.$store.commit(type, resp.cloudFileInfo);
          }
        }
      );
    },
    // 查找文件
    queryFloder(items, type) {
      if (items.pid === '') return;
      this.power_option.isLoading = true;
      console.log(items, this.user_account.id, 'queryFloder params');
      if (!this.user_account.id) {
        this.$service.user.getAccountInfo(null, resp => {
          this.queryFloderFromSdk(items, type, resp.id);
        });
      } else {
        this.queryFloderFromSdk(items, type, this.user_account.id);
      }
    },
    // 获取授权成员
    getMyPermission(items) {
      this.$service.cloud.getRoles({
          isShow: 1
        },
        resp => {
          console.log(resp, '获取我的授权信息');
          let perObj = {};
          let perList = [{
              val: '上传',
              key: 'isUpload'
            },
            {
              val: '下载',
              key: 'isDownload'
            },
            {
              val: '删除',
              key: 'isDelete'
            },
            {
              val: '重命名',
              key: 'isRename'
            },
            {
              val: '创建团队',
              key: 'isCreateFolder'
            }
          ];
          resp.cloudRoleInfo.forEach(element => {
            this.$set(perObj, element.roleId, element.permission);
          });
          this.$service.cloud.teamMembers({
              fileId: items.fileId
            },
            resps => {
              console.log(resps, '成员列表');
              for (let i = 0; i < resps.teamMemberInfo.length; i++) {
                if (resps.teamMemberInfo[i].userId === this.user_account.id) {
                  perList.forEach(element => {
                    perObj[resps.teamMemberInfo[i].roleId].indexOf(element.val) === -1 ?
                      this.$store.commit('SET_POWEROPTION', {
                        key: element.key,
                        value: false
                      }) :
                      this.$store.commit('SET_POWEROPTION', {
                        key: element.key,
                        value: true
                      })
                  })
                }
              }
            }
          )
        }
      )
    },
    // 删除文件
    delFile(items, index, type) {
      if (this.search_list.length > 0) type = 'SET_SEARCHLIST';
      console.log(items, index, type, '删除文件/文件夹');
      this.$alert.confirm({
        confirmText: this.$t('cloud.confirmDelete'),
        title: this.$t('cloud.Warning'),
        // title: '警告！',

        onConfirm: () => {
          console.log(items.fileCode, '删除文件入参');
          this.$service.cloud.delFile(
            [items.fileCode],
            resp => {
              console.log(resp, '删除文件');
              if (resp.code === 0) {
                if (this.power_option.isSearching) {
                  this.$store.commit(
                    'SET_SEARCHLIST', {
                      flag: 0,
                      fileId: items.fileId,
                      index: index,
                      val: this.getLocalFileDecryptPath(items)
                    }
                  );
                }
                this.$store.commit(type, {
                  flag: 0,
                  val: items.fileId
                });
              } else {
                this.$toast.error({
                  message: this.$t('cloud.deleteFailed')
                })
              }
            }
          )
        }
      })
    },
    // 取消上传/下载
    cancelFileOpera(id, index, type, fileId) {
      let cancelTransferParam = {
        localId: id, // 文件的localId
        type: type // 1.上传 2.下载
      }
      console.log(cancelTransferParam, '取消文件上传/下载--入参');
      let cancelTransferCb = (resp) => {
        console.log(resp, '取消上传/下载-返回'); {
          type === 1 ?
            this.$store.commit('SET_UPLOADLIST', {
              flag: 2,
              index: index,
              localId: id
            }) :
            this.$store.commit('SET_DOWNLOADLIST', {
              flag: 2,
              index: index,
              fileId: fileId
            })
        }
      }
      this.$service.file.cancelTransfer(cancelTransferParam, cancelTransferCb)
    },
    // 判断面包屑导航中是否已经存在相同的目录
    noRepeatCrumbs(items, crumbsList) {
      if (JSON.stringify(crumbsList).indexOf(items.fileId) === -1) {
        crumbsList.push({
          text: items.fileName,
          fileId: items.fileId,
          ownerId: items.ownerId,
          fileCode: items.fileCode
        })
      };
      return crumbsList;
    },
    // 共享文件获取根目录内容
    getTeams() {
      this.power_option.isLoading = true;
      this.$service.cloud.getTeams({
          userId: this.user_account.id
        },
        resp => {
          console.log(resp, '共享文件获取根目录内容');
          this.power_option.isLoading = false;
          if (resp.code === 0) {
            this.$store.commit('SET_SHAREFIES', resp.cloudFileInfo)
          }
        }
      )
    }
  }
};

export default mixin;



