<template>
  <li class="node-li">
    <div class="node-item" @click='toggle'>
      <i class="iconfont" :class="isOpenDetail?'icon-zhankai':'icon-111111111'"></i>
      {{model.orgName}}
    </div>
    <ul v-show="isOpenDetail">
      <div v-if="hasChildOrgNode">
        <org-list
          :dataList="model.childUserNode"
          :isOption="false"
          :isCheckItem="true"
          @selsct-item="selsctItem"
        ></org-list>
        <!-- <div
          class="item"
          v-for="(item, index) in model.childUserNode"
          :key="index"
          @click="selsctItem(item, index)"
        >
          <div class="checkbox">
            <input type="checkbox" :checked="item.isChecked">
          </div>
          <div class="portrait">
            <img @error="defaultIcon" :src="item.userHead" />
          </div>
          <div class="name">{{item.enName}}</div>
        </div> -->
        <nodes
          v-for='(cel, index) in model.childOrgNode'
          :key="index"
          :model='cel'
          :folderData="folderData"
          :hasMemberList="hasMemberList"
          @org-selected-users="orgSelectedUsers"
        ></nodes>
      </div>
    </ul>
  </li>
</template>
<script type="text/javascript">
import { mapGetters } from 'vuex';
import orgList from './list'
export default {
  name: 'nodes',
  props: ['model', 'index', 'folderData', 'hasMemberList'],
  components: {
    orgList
  },
  data() {
    return {
      isOpenDetail: false,
      isLoadFinished: false,
      orgId: 0,
      icon: require('./../../assets/img/moren.jpg'),
      selectedUsers: {}
    };
  },
  computed: {
    ...mapGetters(['organization_List', 'orgId_List']),
    hasChildOrgNode() {
      return this.model.childOrgNode || this.model.childUserNode;
    }
  },
  methods: {
    defaultIcon(e) {
      e.target.src = this.icon;
    },
    // 结点 展开/收缩
    toggle() {
      // 断网时展开结点，不再获取数据
      // if (!this.orgId_List.includes(this.model.orgID) && !this.isOpenDetail) return;
      this.isOpenDetail = !this.isOpenDetail;
      this.handleDuplicatedData(this.model.orgID);
    },
    // 处理树结构数据重复请求时，优先拉取缓存数据
    handleDuplicatedData(id) {
      if (this.orgId_List.includes(id)) return;
      this.orgId = id;
      this.setOrganizationList();
    },
    // 获取组织架构数据
    setOrganizationList() {
      let getVisibleOrgUsersParam = {
        type: 0,
        orgId: this.orgId
      };
      this.$service.enterprise.getVisibleOrgUsers(getVisibleOrgUsersParam, resp => {
        console.log('组织架构信息', resp);
        if (resp.code !== 0) return;
        this.$set(this.orgId_List, this.orgId_List.length, this.orgId);
        resp.users.forEach(element => {
          this.$set(
            element,
            'isInclude',
            this.hasMemberList.indexOf(element.userID) !== -1
          );
          this.$set(
            element,
            'isChecked',
            this.hasMemberList.indexOf(element.userID) !== -1
          );
        })
        this.buildTreeStructure(this.organization_List, resp)
      });
    },
    // 递归构造组织架构的树结构
    buildTreeStructure(node, { orgs, users }) {
      node && node.find((element, index) => {
        if (element.orgID === this.orgId) {
          this.$set(element, 'childOrgNode', orgs);
          this.$set(
            element,
            'childUserNode',
            users
          );
          // 保存到树结构后清空，避免注销或者被挤下线，再次登陆数据异常
          this.orgId = 0;
          return;
        } else {
          if (element.childOrgNode && (element.childOrgNode.length !== 0)) {
            this.buildTreeStructure(element.childOrgNode, { orgs, users })
          }
        }
      })
    },
    selsctItem(item, index) {
      console.log(item, 'org-node org user');
      this.$set(this.model.childUserNode[index], 'isChecked', !item.isChecked);
      this.$emit('org-selected-users', item);
      // if (this.selectedUsers.hasOwnProperty(item.userID)) {
      //   this.$delete(this.selectedUsers, item.userID);
      //   this.$emit('org-selected-users', this.selectedUsers);
      //   return
      // };
      // let userData = {
      //   fileId: this.folderData.fileId, // 文件信息
      //   headImg: item.userHead, // 头像(全路径)
      //   createTime: this.folderData.createTime, // 创建时间
      //   updateTime: new Date().getTime().toString(), // 修改时间
      //   userId: item.userID, // 用户ID*
      //   userName: item.enName, // 用户名*
      //   roleId: '2', // 角色Id*
      //   roleName: '普通成员' // 角色名称*
      // };
      // this.$set(this.selectedUsers, item.userID, userData);
      // this.$emit('org-selected-users', this.selectedUsers)
    },
    orgSelectedUsers(item) {
      this.$emit('org-selected-users', item)
    }
  }
};
</script>
<style lang="scss">
.node-li {
  padding: 6px;
  .node-item {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: default;
    i{
      cursor: pointer;
    }
  }
  .item-user-name {
    padding-left: 25px;
    height: 2rem;
    .default-content {
      height: 2rem;
      .title {
        font-size: 14px;
      }
    }
    .item-user-avatar {
      width: 25px;
      height: 25px;
    }
    &:hover {
      background: transparent;
    }
  }
  ul {
    padding: 2px 0px 2px 20px;
    list-style: none;
    overflow: hidden;
    div{
      .item {
        height: 45px;
        padding: 2px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #d1d1d2;
        cursor: pointer;
        &:hover{
          background: #bee9ff91;
        }
        .checkbox{
          width: 27px;
        }
        .portrait {
          width: 35px;
          height: 35px;
          background: #d1d1d2;
          border-radius: 50%;
          overflow: hidden;
          img {
            width: 100%;
            display: block;
          }
        }
        .name {
          width: 55%;
          padding-left: 10px;
        }
      }
    }
  }
}
</style>



