<template>
  <!-- <div v-if="type === 'toast-loading'" class="loading">
      <i class="iconfont icon-jiazaizhong"></i>
      <div class="text">加载中...</div>
  </div>-->
  <transition
    name="custom-classes-transition"
    enter-active-class="animated slideInDown"
    leave-active-class="animated slideOutUp"
  >
    <div class="toast" v-if="isToast">
      <v-alert :type="type" class="pt-2 pb-2 mt-0" value="true" :style="{width:'50%'}">{{message}}</v-alert>
    </div>
  </transition>
</template>
<script>
export default {
  data() {
    return {
      isToast: true
    };
  },
  props: {
    type: {
      type: String,
      default: "toast-tip"
    },
    message: {
      type: String,
      default: "文件正在上传，请在完成后操作"
    }
  },
  computed: {
    toastType() {
      return this.type;
    }
  },
  beforeMount() {
    document.body.appendChild(this.$el);
  },
  methods: {
    close() {
      this.isToast = false;
    }
  },
  mounted() {
    setTimeout(() => {
      this.close();
    }, 3000);
  }
};
</script>

<style lang="scss" scoped>
@keyframes loading {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.toast {
  z-index: 1000;
  position: fixed;
  top: 0px;
  width: 100%;
  margin: 0 auto;
}
</style>



