<template>
  <v-layout row justify-center>
    <v-dialog
      :value="powerModal_state"
      class="modal-dialog"
      absolute
      scrollable
      persistent
      max-width="400px"
    >
      <v-card>
        <v-card-title class="pt-2 pb-2" :style="titleStyle">
          {{$t('cloud.author')}}
          <v-icon
            color="white"
            :title="$t('cloud.addMember')"
            @click="isAddMember = true"
            :disabled="hasMemberUserId.length>0?false:true"
          >person_add</v-icon>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text style="height: 300px" class="pr-0 pl-0 pt-0">
          <v-expansion-panel v-model="panel" v-if="hasMemberList.length > 0" expand>
            <v-expansion-panel-content v-for="(item, index) in power_MemberList" :key="index">
              <template v-slot:header v-if="item.memberList && item.memberList.length > 0">
                <div>{{item.name}}</div>
              </template>
              <c-list
                v-if="item.memberList && item.memberList.length > 0"
                :dataList="item.memberList"
                @change-pwore="changePwore"
                @del-member="delMember"
              ></c-list>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions :style="{'justify-content':'flex-end'}">
          <v-btn color="info" @click="cancel" small>{{$t('cloud.close')}}</v-btn>
        </v-card-actions>
      </v-card>
      <v-bottom-sheet v-model="isResetMember" inset>
        <v-list>
          <v-subheader>{{$t('cloud.selectRoles')}}</v-subheader>
          <v-list-tile
            v-for="(item, index) in power_MemberList"
            :key="index"
            @click="setRoles(item, index)"
            v-if="item.roleId !== tempSelectItem.roleId"
          >
            <v-list-tile-title>{{item.name}}</v-list-tile-title>
          </v-list-tile>
        </v-list>
      </v-bottom-sheet>
    </v-dialog>
    <add-member
      v-if="hasMemberUserId.length>0?true:false"
      :isShow="isAddMember"
      :folderData="folderData"
      :hasMemberList="hasMemberUserId"
      @selscted-user="teamResetMember"
      @close="isAddMember = false"
      ref="addMember"
    ></add-member>
  </v-layout>
</template>

<script>
import { mapGetters } from "vuex";
import cloudMixin from "./../common/cloudMixin.js";
import AddMember from "./add-member.vue";
import CList from "./list";
export default {
  mixins: [cloudMixin],
  data() {
    return {
      panel: [],
      isAddMember: false,
      isResetMember: false,
      tempSelectItem: {},
      tempSelectIndex: -1,
      hasMemberObj: {},
      rolesList: [],
      titleStyle: {
        "justify-content": "space-between",
        "background-color": "#1E88E5",
        color: "#fff"
      },
      navToolBarStyle: {
        position: "sticky",
        top: "0px",
        "z-index": "2"
      }
    };
  },
  props: {
    folderData: Object
  },
  components: {
    CList,
    AddMember
  },
  computed: {
    ...mapGetters([
      "user_account",
      "share_files",
      "search_list",
      "power_option",
      "power_MemberList",
      "orgId_List",
      "powerModal_state"
    ]),
    hasMemberUserId() {
      return Object.keys(this.hasMemberObj);
    },
    hasMemberList() {
      return Object.values(this.hasMemberObj);
    }
  },
  created() {
    this.getMemberList();
    this.setOrganizationList();
  },
  watch: {
    power_MemberList(newV) {
      let length = newV.length;
      for (let count = 0; count < length; count++) {
        this.panel.push("true");
      }
    }
  },
  methods: {
    // 获取组织架构数据
    setOrganizationList() {
      let getVisibleOrgUsersParam = { type: 0, orgId: 0 };
      this.$service.enterprise.getVisibleOrgUsers(
        getVisibleOrgUsersParam,
        resp => {
          console.log("组织架构信息", resp);
          if (resp.code !== 0) return;
          this.$set(this.orgId_List, 0, 0);
          resp.users.forEach(element => {
            this.$set(element, "isChecked", false);
          });
          this.$store.commit("SET_ORGANIZATON_LIST", resp.orgs);
        }
      );
    },
    // 获取成员列表
    getMemberList() {
      this.$service.cloud.getRoles({ isShow: 1 }, resp => {
        console.log(resp, "getRoles callback");
        if (resp.code === 0) {
          // 角色列表
          this.rolesList = resp.cloudRoleInfo;
          this.$service.cloud.teamMembers(
            { fileId: this.folderData.fileId },
            resps => {
              console.log(resps, "teamMembers callback");
              if (resps.code === 0) {
                // this.hasMemberList = resp.teamMemberInfo;
                // 设置 包含的权限成员
                resps.teamMemberInfo.forEach(element => {
                  this.$set(this.hasMemberObj, element.userId, element);
                });
                this.rolesList.forEach(rolesItem => {
                  // 设置角色列表包含的 成员
                  this.$set(rolesItem, "memberList", []);
                  // 遍历包含的权限成员列表
                  this.hasMemberList.forEach(memberItem => {
                    if (memberItem.userId !== this.user_account.id) {
                      if (rolesItem.roleId === memberItem.roleId) {
                        rolesItem.memberList.push(memberItem);
                      }
                    }
                  });
                });
                this.$store.commit("SET_POWERMEMBERlIST", this.rolesList);
                this.isAddMember = false;
              }
            }
          );
        }
      });
    },
    // 关闭设置权限弹框
    cancel() {
      this.$store.commit("SET_POWERMODAL", false);
      this.$store.commit("SET_ORGANIZATON_LIST", 0);
    },
    // 添加成员
    teamResetMember(selectMemberParam) {
      let addMemberParam = selectMemberParam.concat(this.hasMemberList);
      console.log(addMemberParam, "重置授权人员--入参");
      this.cloudTeamResetMember(addMemberParam);
    },
    // 删除成员
    delMember(item, index) {
      console.log("del-member", item, index);
      this.$alert.confirm({
        confirmText: this.$t("cloud.confirmDelete"),
        // title: '警告！',
        title: this.$t("cloud.Warning"),
        onConfirm: () => {
          // let delMemberObj = Object.assign({}, this.hasMemberObj);
          this.$delete(this.hasMemberObj, item.userId);
          // let delMemberParam = Object.values(delMemberObj);
          this.cloudTeamResetMember(this.hasMemberList);
          // this.$store.commit('SET_POWERMEMBERlIST', this.rolesList)
        }
      });
    },
    // 更改权限，选中当前成员
    changePwore(item, index) {
      this.isResetMember = true;
      this.tempSelectItem = item;
      this.tempSelectIndex = index;
      // this.isResetMember = true;
      console.log(this.tempSelectItem);
    },
    // 重新设置角色
    setRoles(item, index) {
      console.log(item, index, "------------");
      this.isResetMember = false;
      let setRolesObj = Object.assign({}, this.hasMemberObj);
      setRolesObj[this.tempSelectItem.userId].roleId = item.roleId;
      setRolesObj[this.tempSelectItem.userId].roleName = item.name;
      let setRolesParam = Object.values(setRolesObj);
      this.cloudTeamResetMember(setRolesParam);
    },
    // 调用接口重置授权人员
    cloudTeamResetMember(teamMemberParam) {
      this.$service.cloud.teamResetMember(teamMemberParam, resp => {
        console.log(resp, "重置授权人员--返回");
        if (resp.code === 0) {
          this.getMemberList();
          this.isAddMember = false;
          // this.$toast.tip({message: '设置成功'});
          //this.$toast.tip({message: this.$t('cloud.setupSuccessfully')});
        } else {
          this.$toast.error({ message: this.$t("cloud.setupFailed") });
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
</style>



