<template>
  <transition
    name="custom-classes-transition"
    enter-active-class="animated pulse"
    leave-active-class="animated zoomOut"
  >
    <div class="loacl-search" v-click-outside="onClickOutside" v-if="showSearchInput">
      <v-menu transition="slide-y-transition" offset-y>
        <template v-slot:activator="{ on }">
          <v-btn
            class="purple pr-0 pl-2"
            color="primary"
            light
            depressed
            v-on="on"
            :style="searchButtonStyle"
            @click="showTypeList=!showTypeList"
          >
            {{searchTypeList[currentSearchType].simple}}
            <v-icon v-show="!showTypeList">arrow_drop_down</v-icon>
            <v-icon v-show="showTypeList">arrow_drop_up</v-icon>
          </v-btn>
        </template>
        <v-list>
          <v-list-tile v-for="(item, index) in searchTypeList" :key="index" @click>
            <v-list-tile-title @click="currentSearchType = index;showTypeList=false">
              {{ item.val }}
              <v-icon v-if="index === currentSearchType">done</v-icon>
            </v-list-tile-title>
          </v-list-tile>
        </v-list>
      </v-menu>
      <v-text-field
        solo
        flat
        hide-details
        clearable
        autofocus
        height="28px"
        :label="$t('cloud.search')"
        prepend-inner-icon="search"
        @keyup.enter="searchFile"
        v-model="keyWords"
        color="grey  lighten-1"
      ></v-text-field>
    </div>
  </transition>
</template>

<script>
import { mapGetters } from "vuex";
import vClickOutside from "../v-click-outside/index.js";
import cloudMixin from "../cloudMixin.js";
export default {
  mixins: [cloudMixin],
  directives: {
    clickOutside: vClickOutside.directive
  },
  data() {
    return {
      currentSearchType: 0,
      searchTypeList: [
        {
          val: this.$t("cloud.searchbyAll"),
          simple: this.$t("cloud.searchbyAllSimple"),
          type: 3
        },
        {
          val: this.$t("cloud.searchbyFile"),
          simple: this.$t("cloud.searchbyFileSimple"),
          type: 1
        },
        {
          val: this.$t("cloud.searchbyName"),
          simple: this.$t("cloud.searchbyNameSimple"),
          type: 2
        }
      ],
      showTypeList: false,
      isFoucs: false,
      keyWords: "",
      searchButtonStyle: {
        height: "28px",
        minWidth: "76px",
        maxWidth: "150px",
        overflow: "hidden"
      }
    };
  },
  props: {
    showSearchInput: Boolean
  },
  watch: {
    keyWords(val) {
      if (val === "") {
        // 当输入的结果清空时，则清空vuex搜索列表的数据
        this.$store.commit("SET_SEARCHLIST", { flag: 1, val: [] });
      }
    },
    showSearchInput(val) {
      console.log("searchInputShow::", val);
      if (val) {
        this.openSearch();
        return true;
      } else {
        this.closeSearch();
        return false;
      }
    }
  },
  computed: {
    ...mapGetters(["user_account", "power_option"]),
    cursorType() {
      return this.isFoucs ? "" : { cursor: "pointer" };
    }
  },
  methods: {
    openSearch() {
      //this.isFoucs = true;
      this.$store.commit("SET_POWEROPTION", {
        key: "isSearching",
        value: true
      });
    },
    closeSearch() {
      this.keyWords = "";
      this.showTypeList = false;
      this.isFoucs = false;
      this.$store.commit("SET_POWEROPTION", {
        key: "isSearching",
        value: false
      });
      this.$store.commit("SET_SEARCHLIST", { flag: 1, val: [] });
    },
    onClickOutside(event) {
      if (
        (this.keyWords !== "" && this.keyWords !== null) ||
        !this.showSearchInput
      )
        return;
      this.$emit("close");
    },
    searchFile() {
      if (this.keyWords === "") {
        return;
      }
      if (!this.isFoucs) {
        this.isFoucs = true;
        return;
      }
      this.$store.commit("SET_SEARCHLIST", { flag: 1, val: [] });
      this.power_option.isLoading = true;
      let searchFilesParam = {
        userId: this.user_account.id,
        searchText: this.keyWords
      };
      // type: this.searchTypeList[this.currentSearchType].type
      console.log(searchFilesParam, "搜索--入参");
      this.$service.cloud.searchFiles(searchFilesParam, resp => {
        console.log(resp, "搜索结果");
        this.power_option.isLoading = false;
        if (resp.code === 0) {
          if (resp.cloudFileInfo.length === 0) {
            this.$toast.info({ message: this.$t("cloud.searhNoth") });
          }
          resp.cloudFileInfo.forEach(element => {
            if (element.fileType === 1) {
              this.$set(element, "fileLocalPath", "");
              let fileLocalPath = "";
              let hasFile = false;
              if (element.hasOwnProperty("encryptKey") && element.encryptKey) {
                fileLocalPath = this.getLocalEncryptFilePath(element); //设置源文件本地路径
                hasFile = this.$service.file.fileOperate({
                  filepath: fileLocalPath,
                  type: 0
                });
              } else {
                fileLocalPath = this.getLocalFileDecryptPath(element); //设置源文件本地路径
                hasFile = this.$service.file.fileOperate({
                  filepath: this.getLocalFileDecryptPath(element),
                  type: 0
                });
              }
              if (hasFile) element.fileLocalPath = fileLocalPath;
            }
          });
          this.$store.commit("SET_SEARCHLIST", {
            flag: 1,
            val: resp.cloudFileInfo
          });
        } else {
          this.$toast.warning({ message: resp.code });
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.loacl-search {
  align-items: center;
  display: flex;
  .zoom-in-top-enter-active,
  .zoom-in-top-leave-active {
    opacity: 1;
    transform: scaleY(1);
    transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1) 100ms,
      opacity 300ms cubic-bezier(0.23, 1, 0.32, 1) 100ms;
    transform-origin: center top;
  }
  .zoom-in-top-enter,
  .zoom-in-top-leave-active {
    opacity: 0;
    transform: scaleY(0);
  }
}
</style>



